
import React, {useState} from 'react';
import { useHistory } from "react-router-dom";

const PopupLogin = (props) =>{
    let history = useHistory();
    const [email, setEmail] = useState('')
    const [password, setPassword] = useState('')
    const [error, setError] = useState('')
    const loginUser = async user => {
        try {
            const rawResponse = await fetch('https://react-rslang-de.herokuapp.com/signin', {
                method: 'POST',
                headers: {
                    'Accept': 'application/json',
                    'Content-Type': 'application/json'
                },
                body: JSON.stringify(user)
            });
            const content = await rawResponse.json();

            localStorage.setItem('userData', JSON.stringify(content))
            setTimeout(() => {
                localStorage.removeItem("userData"); // Чтоб удалить конкретно userData из localStorage
            }, 1000 * 3600 * 4); // удалить данные через 4 часа
            props.hidePopup()
            history.push('/profile')
        }
        catch (e){
            setError('Неверные данные')
        }

    };
    const singIn = () =>{
        loginUser({ "email": email, "password": password});

        history.push("/profile");

    }


    return(
        <>
            <div className="popup popup-login">
                <form action="" className='flex-column'>
                    <div className="popup__title">Войдите</div>

                    <input type="email"
                           value={email}
                           onChange={e => setEmail(e.target.value)}
                           placeholder='Email'/>
                    <input type="password"
                           value={password}
                           onChange={e => setPassword(e.target.value)}
                           placeholder='Пароль'/>
                    <a href='#' className="btn" onClick={singIn}>
                        Войти
                    </a>
                    <div className='error'>{error}</div>
                </form>

            </div>
        </>
    )
}

export default PopupLogin;