
import React, {useState} from 'react';
const PopupRegister = () =>{
    const [email, setEmail] = useState('')
    const [password, setPassword] = useState('')
    const [error, setError] = useState('')
    const createUser = async user => {
        try{
            const rawResponse = await fetch('https://react-rslang-de.herokuapp.com/users', {
                method: 'POST',
                headers: {
                    'Accept': 'application/json',
                    'Content-Type': 'application/json'
                },
                body: JSON.stringify(user)
            });
            const content = await rawResponse.json();
            localStorage.setItem('userData', JSON.stringify(content))
            console.log(content);
            window.location.reload();
        } catch (e){
            setError('Неверные данные, либо пользователь уже зарегистрирован')
        }

    };
    const registerUser = () =>{
        createUser({ "email": email, "password": password });

    }

    return(

        <>
            <div className="popup popup-login">
                <form action="" className='flex-column'  >
                    <div className="popup__title">Регистрация</div>
                    <input type="text" placeholder='Логин'/>
                    <input type="email"
                           value={email}
                           onChange={e => setEmail(e.target.value)}
                           placeholder='Email'/>
                    <input type="password"
                           value={password}
                           onChange={e => setPassword(e.target.value)}
                           placeholder='Пароль'/>
                    <label htmlFor="">Ваш пароль должен содержать по крайней мере 8 символов, одну прописную, одну строчную букву и специальный символ</label>
                    <div className="file-wrapper">
                        <input
                            type="file"
                            accept=".png, .jpg, .jpeg"
                            name="photo"
                        />
                        <span>+ ЗАГРУЗИТЕ ФОТО</span>
                    </div>
                    <div className='file-name'></div>
                    <a href='#' onClick={registerUser} className="btn">
                        Зарегистрироваться
                    </a>
                    <div className='error'>{error}</div>
                </form>

            </div>
        </>
    )
}

export default PopupRegister;