import React, {useState, useEffect} from "react"
import './MemoryField.scss'
import Countdown from '../../SprintGame/Countdown/Countdown'
import StatisticGameSprint from '../../SprintGame/StatisticGameSprint/StatisticGameSprint'
import MemoryContainer from '../MemoryContainer/MemoryContainer'
import {useMemoryStatistics} from '../../Statistics/StatisticsContext'


const MemoryField = (props) => {
    const [countDownGame, setCountDownGame] = useState(60)
    const [isVisiblecountDownGame, setIsVisiblecountDownGame] = useState(false)
    const [isVisibleGameField, setIsVisibleGameField] = useState(true)
    const [isSound, setIsSound] = useState(true)
    const [dataEnWords, setDataEnWords] = useState(null)
    const [dataRuWords, setDataRuWords] = useState(null)
    const [isFirstLive, setIsFirstLive] = useState(true)
    const [isSecondLive, setIsSecondLive] = useState(true)
    const [isThirdLive, setIsThirdLive] = useState(true)
    const [isFirstLevel, setIsFirstLevel] = useState(true)
    const [isSecondLevel, setIsSecondLevel] = useState(false)
    const [isThirdLevel, setIsThirdLevel] = useState(false)
    const [isFourthLevel, setIsFourthLevel] = useState(false)
    const [isFifthLevel, setIsFifthLevel] = useState(false)
    const [isSixthLevel, setIsSixthLevel] = useState(false)
    const [arrayCorrectAnswer, setArrayCorrectAnswer] = useState([])
    const [arrayUnCorrectAnswer, setArrayUnCorrectAnswer] = useState([])
    const [clickingCardEn, setClickingCardEn] = useState([])
    const [clickingCardRu, setClickingCardRu] = useState([])
    const [match, setMatch] = useState([])
    const [countFalse, setCountFalse] = useState(0)

    const [isLoader, setIsLoader] = useState(true)




    const audioShowResult = new Audio('/audio/show_result.mp3')
    useEffect(() => {
        if (dataEnWords || dataRuWords) {
            setIsLoader(false)

        }
    })
    useEffect(() => {
        if (!isLoader) {
            setIsVisiblecountDownGame(true)

        }
    }, [isLoader])

    useEffect(() => {
        if (isFirstLevel) {
            let url = 'https://react-rslang-de.herokuapp.com/words?page=3&group=0'
            fetch(url)
                .then(res => res.json())
                .then(data => {
                    data ? setDataEnWords((data.slice(0, 10)).sort(() => Math.random() - 0.5)) : setDataEnWords(null)
                    data ? setDataRuWords((data.slice(0, 10)).sort(() => Math.random() - 0.5)) : setDataRuWords(null)
                })
        }
    }, [isFirstLevel])
    useEffect(() => {
        if (isSecondLevel) {
            let url = 'https://react-rslang-de.herokuapp.com/words?page=10&group=1'
            fetch(url)
                .then(res => res.json())
                .then(data => {
                    data ? setDataEnWords((data.slice(0, 10)).sort(() => Math.random() - 0.5)) : setDataEnWords(null)
                    data ? setDataRuWords((data.slice(0, 10)).sort(() => Math.random() - 0.5)) : setDataRuWords(null)
                })
        }
    }, [isSecondLevel])
    useEffect(() => {
        if (isThirdLevel) {
            let url = 'https://react-rslang-de.herokuapp.com/words?page=3&group=2'
            fetch(url)
                .then(res => res.json())
                .then(data => {
                    data ? setDataEnWords((data.slice(0, 10)).sort(() => Math.random() - 0.5)) : setDataEnWords(null)
                    data ? setDataRuWords((data.slice(0, 10)).sort(() => Math.random() - 0.5)) : setDataRuWords(null)
                })
        }
    }, [isThirdLevel])
    useEffect(() => {
        if (isFourthLevel) {
            let url = 'https://react-rslang-de.herokuapp.com/words?page=10&group=3'
            fetch(url)
                .then(res => res.json())
                .then(data => {
                    data ? setDataEnWords((data.slice(0, 10)).sort(() => Math.random() - 0.5)) : setDataEnWords(null)
                    data ? setDataRuWords((data.slice(0, 10)).sort(() => Math.random() - 0.5)) : setDataRuWords(null)
                })
        }
    }, [isFourthLevel])
    useEffect(() => {
        if (isFifthLevel) {
            let url = 'https://react-rslang-de.herokuapp.com/words?page=24&group=4'
            fetch(url)
                .then(res => res.json())
                .then(data => {
                    data ? setDataEnWords((data.slice(0, 10)).sort(() => Math.random() - 0.5)) : setDataEnWords(null)
                    data ? setDataRuWords((data.slice(0, 10)).sort(() => Math.random() - 0.5)) : setDataRuWords(null)
                })
        }
    }, [isFifthLevel])
    useEffect(() => {
        if (isSixthLevel) {
            let url = 'https://react-rslang-de.herokuapp.com/words?page=24&group=5'
            fetch(url)
                .then(res => res.json())
                .then(data => {
                    data ? setDataEnWords((data.slice(0, 10)).sort(() => Math.random() - 0.5)) : setDataEnWords(null)
                    data ? setDataRuWords((data.slice(0, 10)).sort(() => Math.random() - 0.5)) : setDataRuWords(null)
                })
        }
    }, [isSixthLevel])

    useEffect(() => {
        let timer
        if (countDownGame > 0) {
            timer = setTimeout(() => {
                setCountDownGame(countDownGame - 1)
                if (countDownGame <= 4 && countDownGame > 0 && isSound) {
                    props.audioFinish.play()
                }
            }, 1000);
        } else {
            setCountDownGame(null)
            setIsVisibleGameField(false)
            if (countDownGame > 4 && countDownGame < 0 && !isVisibleGameField && !isSound) {
                props.audioFinish.pause()
            }
        }

    }, [countDownGame])
    useEffect(() => {
        if (!isThirdLive) {
            setCountDownGame(null)
            setIsVisibleGameField(false)
        }
    })
    useEffect(() => {
        if (countFalse === 0) {
            setIsFirstLive(true)
            setIsSecondLive(true)
            setIsThirdLive(true)
        }
    }, [countFalse])
    const handlerSound = () => {
        setIsSound(isSound === false ? true : false)
    }
    return (
        <>
            {isVisibleGameField ? (<div className={'game_container'}>
                {isVisiblecountDownGame && <div className={'countdown_box'}>
                    <Countdown countDownGame={countDownGame}/>
                </div>}
                <div className={'box_heart'}>
                    {isThirdLive ? (<img
                        src={'data:image/svg+xml;base64,'}
                        alt={'heart'}/>) : ''}
                    {isSecondLive ? (<img
                        src={'data:image/svg+xml;base64,'}
                        alt={'heart'}/>) : ''}
                    {isFirstLive ? (<img
                        src={'data:image/svg+xml;base64,'}
                        alt={'heart'}/>) : ''}
                </div>
                <div className={'sound_icon'} onClick={handlerSound}>{isSound ? (<img
                    src={'data:image/svg+xml;base64,'}
                    alt={'sound'}/>) : (
                    <img
                        src={'data:image/svg+xml;base64,'}
                        alt={'sound'}/>)}</div>
                <div className={'level_container'}>
                    <div>Уровень:</div>
                    <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24"
                         className={`${isFirstLevel ? 'active_level' : ''}`}
                         onClick={() => {
                             setIsFirstLevel(true)
                             setIsSecondLevel(false)
                             setIsThirdLevel(false)
                             setIsFourthLevel(false)
                             setIsFifthLevel(false)
                             setIsSixthLevel(false)
                             setMatch([])
                             // setCountFalse(0)
                             // setCountDownGame(60)
                         }}>
                        <path fill="#20A3FA" fillRule="evenodd"
                              d="M12 24C5.373 24 0 18.627 0 12S5.373 0 12 0s12 5.373 12 12-5.373 12-12 12zM4.805 12.9c.084.96 1.005 1.869 2.048 2.02 1.043.152 2.033 1.142 2.2 2.2.167 1.058 1.088 1.992 2.047 2.076.96.083 2.018-.94 2.352-2.276.334-1.335.47-3.314.303-4.4-.166-1.084-1.191-2.108-2.275-2.274-1.085-.168-3.065-.032-4.4.303-1.334.333-2.358 1.392-2.275 2.351zm3.978-4.305c.713 0 1.387-.428 1.666-1.131l.199-.503a1.79 1.79 0 10-3.33-1.32l-.199.503a1.79 1.79 0 001.664 2.451zm5.095-.265a1.79 1.79 0 001.537-.87l.448-.747a1.79 1.79 0 00-.615-2.457 1.79 1.79 0 00-2.457.614l-.448.748a1.79 1.79 0 001.535 2.712zm3.319 8.687c.22 0 .444-.041.66-.127l.503-.2a1.791 1.791 0 00-1.322-3.328l-.502.199a1.792 1.792 0 00.66 3.456zm.267-5.11c.31 0 .626-.08.912-.251l.746-.442a1.79 1.79 0 10-1.827-3.081l-.747.443a1.791 1.791 0 00.916 3.331z"></path>
                    </svg>
                    <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24"
                         className={`${isSecondLevel ? 'active_level' : ''}`}
                         onClick={() => {
                             setIsFirstLevel(false)
                             setIsSecondLevel(true)
                             setIsThirdLevel(false)
                             setIsFourthLevel(false)
                             setIsFifthLevel(false)
                             setIsSixthLevel(false)
                             setMatch([])
                             // setCountFalse(0)
                             // setCountDownGame(60)
                         }}>
                        <path fill=" #739094 " fillRule="evenodd"
                              d="M12 24C5.373 24 0 18.627 0 12S5.373 0 12 0s12 5.373 12 12-5.373 12-12 12zM4.805 12.9c.084.96 1.005 1.869 2.048 2.02 1.043.152 2.033 1.142 2.2 2.2.167 1.058 1.088 1.992 2.047 2.076.96.083 2.018-.94 2.352-2.276.334-1.335.47-3.314.303-4.4-.166-1.084-1.191-2.108-2.275-2.274-1.085-.168-3.065-.032-4.4.303-1.334.333-2.358 1.392-2.275 2.351zm3.978-4.305c.713 0 1.387-.428 1.666-1.131l.199-.503a1.79 1.79 0 10-3.33-1.32l-.199.503a1.79 1.79 0 001.664 2.451zm5.095-.265a1.79 1.79 0 001.537-.87l.448-.747a1.79 1.79 0 00-.615-2.457 1.79 1.79 0 00-2.457.614l-.448.748a1.79 1.79 0 001.535 2.712zm3.319 8.687c.22 0 .444-.041.66-.127l.503-.2a1.791 1.791 0 00-1.322-3.328l-.502.199a1.792 1.792 0 00.66 3.456zm.267-5.11c.31 0 .626-.08.912-.251l.746-.442a1.79 1.79 0 10-1.827-3.081l-.747.443a1.791 1.791 0 00.916 3.331z"></path>
                    </svg>
                    <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24"
                         className={`${isThirdLevel ? 'active_level' : ''}`}
                         onClick={() => {
                             setIsFirstLevel(false)
                             setIsSecondLevel(false)
                             setIsThirdLevel(true)
                             setIsFourthLevel(false)
                             setIsFifthLevel(false)
                             setIsSixthLevel(false)
                             setClickingCardEn([])
                             setClickingCardRu([])
                             setMatch([])
                             // setCountFalse(0)
                             // setCountDownGame(60)
                         }}>
                        <path fill="#5FFAB4" fillRule="evenodd"
                              d="M12 24C5.373 24 0 18.627 0 12S5.373 0 12 0s12 5.373 12 12-5.373 12-12 12zM4.805 12.9c.084.96 1.005 1.869 2.048 2.02 1.043.152 2.033 1.142 2.2 2.2.167 1.058 1.088 1.992 2.047 2.076.96.083 2.018-.94 2.352-2.276.334-1.335.47-3.314.303-4.4-.166-1.084-1.191-2.108-2.275-2.274-1.085-.168-3.065-.032-4.4.303-1.334.333-2.358 1.392-2.275 2.351zm3.978-4.305c.713 0 1.387-.428 1.666-1.131l.199-.503a1.79 1.79 0 10-3.33-1.32l-.199.503a1.79 1.79 0 001.664 2.451zm5.095-.265a1.79 1.79 0 001.537-.87l.448-.747a1.79 1.79 0 00-.615-2.457 1.79 1.79 0 00-2.457.614l-.448.748a1.79 1.79 0 001.535 2.712zm3.319 8.687c.22 0 .444-.041.66-.127l.503-.2a1.791 1.791 0 00-1.322-3.328l-.502.199a1.792 1.792 0 00.66 3.456zm.267-5.11c.31 0 .626-.08.912-.251l.746-.442a1.79 1.79 0 10-1.827-3.081l-.747.443a1.791 1.791 0 00.916 3.331z"></path>
                    </svg>
                    <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24"
                         className={`${isFourthLevel ? 'active_level' : ''}`}
                         onClick={() => {
                             setIsFirstLevel(false)
                             setIsSecondLevel(false)
                             setIsThirdLevel(false)
                             setIsFourthLevel(true)
                             setIsFifthLevel(false)
                             setIsSixthLevel(false)
                             setClickingCardEn([])
                             setClickingCardRu([])
                             setMatch([])
                             // setCountFalse(0)
                             // setCountDownGame(60)
                         }}>
                        <path fill="#FB9EA0" fillRule="evenodd"
                              d="M12 24C5.373 24 0 18.627 0 12S5.373 0 12 0s12 5.373 12 12-5.373 12-12 12zM4.805 12.9c.084.96 1.005 1.869 2.048 2.02 1.043.152 2.033 1.142 2.2 2.2.167 1.058 1.088 1.992 2.047 2.076.96.083 2.018-.94 2.352-2.276.334-1.335.47-3.314.303-4.4-.166-1.084-1.191-2.108-2.275-2.274-1.085-.168-3.065-.032-4.4.303-1.334.333-2.358 1.392-2.275 2.351zm3.978-4.305c.713 0 1.387-.428 1.666-1.131l.199-.503a1.79 1.79 0 10-3.33-1.32l-.199.503a1.79 1.79 0 001.664 2.451zm5.095-.265a1.79 1.79 0 001.537-.87l.448-.747a1.79 1.79 0 00-.615-2.457 1.79 1.79 0 00-2.457.614l-.448.748a1.79 1.79 0 001.535 2.712zm3.319 8.687c.22 0 .444-.041.66-.127l.503-.2a1.791 1.791 0 00-1.322-3.328l-.502.199a1.792 1.792 0 00.66 3.456zm.267-5.11c.31 0 .626-.08.912-.251l.746-.442a1.79 1.79 0 10-1.827-3.081l-.747.443a1.791 1.791 0 00.916 3.331z"></path>
                    </svg>
                    <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24"
                         className={`${isFifthLevel ? 'active_level' : ''}`}
                         onClick={() => {
                             setIsFirstLevel(false)
                             setIsSecondLevel(false)
                             setIsThirdLevel(false)
                             setIsFourthLevel(false)
                             setIsFifthLevel(true)
                             setIsSixthLevel(false)
                             setClickingCardEn([])
                             setClickingCardRu([])
                             setMatch([])
                             // setCountFalse(0)
                             // setCountDownGame(60)
                         }}>
                        <path fill="#38B7C7" fillRule="evenodd"
                              d="M12 24C5.373 24 0 18.627 0 12S5.373 0 12 0s12 5.373 12 12-5.373 12-12 12zM4.805 12.9c.084.96 1.005 1.869 2.048 2.02 1.043.152 2.033 1.142 2.2 2.2.167 1.058 1.088 1.992 2.047 2.076.96.083 2.018-.94 2.352-2.276.334-1.335.47-3.314.303-4.4-.166-1.084-1.191-2.108-2.275-2.274-1.085-.168-3.065-.032-4.4.303-1.334.333-2.358 1.392-2.275 2.351zm3.978-4.305c.713 0 1.387-.428 1.666-1.131l.199-.503a1.79 1.79 0 10-3.33-1.32l-.199.503a1.79 1.79 0 001.664 2.451zm5.095-.265a1.79 1.79 0 001.537-.87l.448-.747a1.79 1.79 0 00-.615-2.457 1.79 1.79 0 00-2.457.614l-.448.748a1.79 1.79 0 001.535 2.712zm3.319 8.687c.22 0 .444-.041.66-.127l.503-.2a1.791 1.791 0 00-1.322-3.328l-.502.199a1.792 1.792 0 00.66 3.456zm.267-5.11c.31 0 .626-.08.912-.251l.746-.442a1.79 1.79 0 10-1.827-3.081l-.747.443a1.791 1.791 0 00.916 3.331z"></path>
                    </svg>
                    <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24"
                         className={`${isSixthLevel ? 'active_level' : ''}`}
                         onClick={() => {
                             setIsFirstLevel(false)
                             setIsSecondLevel(false)
                             setIsThirdLevel(false)
                             setIsFourthLevel(false)
                             setIsFifthLevel(false)
                             setIsSixthLevel(true)
                             setClickingCardEn([])
                             setClickingCardRu([])
                             setMatch([])
                             // setCountFalse(0)
                             // setCountDownGame(60)
                         }}>
                        <path fill="#D614FA" fillRule="evenodd"
                              d="M12 24C5.373 24 0 18.627 0 12S5.373 0 12 0s12 5.373 12 12-5.373 12-12 12zM4.805 12.9c.084.96 1.005 1.869 2.048 2.02 1.043.152 2.033 1.142 2.2 2.2.167 1.058 1.088 1.992 2.047 2.076.96.083 2.018-.94 2.352-2.276.334-1.335.47-3.314.303-4.4-.166-1.084-1.191-2.108-2.275-2.274-1.085-.168-3.065-.032-4.4.303-1.334.333-2.358 1.392-2.275 2.351zm3.978-4.305c.713 0 1.387-.428 1.666-1.131l.199-.503a1.79 1.79 0 10-3.33-1.32l-.199.503a1.79 1.79 0 001.664 2.451zm5.095-.265a1.79 1.79 0 001.537-.87l.448-.747a1.79 1.79 0 00-.615-2.457 1.79 1.79 0 00-2.457.614l-.448.748a1.79 1.79 0 001.535 2.712zm3.319 8.687c.22 0 .444-.041.66-.127l.503-.2a1.791 1.791 0 00-1.322-3.328l-.502.199a1.792 1.792 0 00.66 3.456zm.267-5.11c.31 0 .626-.08.912-.251l.746-.442a1.79 1.79 0 10-1.827-3.081l-.747.443a1.791 1.791 0 00.916 3.331z"></path>
                    </svg>

                </div>
                <div className={'memory_container'}>
                    <MemoryContainer setArrayCorrectAnswer={(value) => setArrayCorrectAnswer(value)}
                                     setArrayUnCorrectAnswer={(value) => setArrayUnCorrectAnswer(value)}
                                     arrayCorrectAnswer={arrayCorrectAnswer}
                                     arrayUnCorrectAnswer={arrayUnCorrectAnswer}
                                     dataEnWords={dataEnWords} dataRuWords={dataRuWords}
                                     setIsVisibleGameField={(value) => setIsVisibleGameField(value)}
                                     isSound={isSound}
                                     setIsFirstLive={(value) => setIsFirstLive(value)}
                                     setIsSecondLive={(value) => setIsSecondLive(value)}
                                     setIsThirdLive={(value) => setIsThirdLive(value)}
                                     isThirdLive={isThirdLive}
                                     isSecondLive={isSecondLive}
                                     isFirstLive={isFirstLive}
                                     clickingCardEn={clickingCardEn}
                                     clickingCardRu={clickingCardRu}
                                     setClickingCardEn={(value) => setClickingCardEn(value)}
                                     setClickingCardRu={(value) => setClickingCardRu(value)}
                                     isVisibleGameField={isVisibleGameField}
                                     match={match}
                                     setMatch={(value) => setMatch(value)}
                                     countFalse={countFalse}
                                     setCountFalse={(value) => setCountFalse(value)}
                                     isLoader={isLoader}
                    />
                </div>
            </div>) : (<div className={'statistic_box'}><StatisticGameSprint
                setIsVisibleGameField={props.setIsVisibleGameField}
                isVisiblecountDownGame={isVisiblecountDownGame}
                isSound={isSound}
                setIsVisibleStartCount={props.setIsVisibleStartCount}
                setCountStart={props.setCountStart}
                audioShowResult={audioShowResult}
                audioFinish={props.audioFinish}
                arrayCorrectAnswer={arrayCorrectAnswer}
                arrayUnCorrectAnswer={arrayUnCorrectAnswer}
                // setIsVisibleBtnClose={props.setIsVisibleBtnClose}
                setMatch={(value) => setMatch(value)}
                setCountFalse={(value) => setCountFalse(value)}
                // setCountDownGame={(value)=>setCountDownGame(value)}
            /></div>)}
        </>
    )
}
export default MemoryField


