import { useState, useEffect } from 'react'
import './CurrentWord.scss'
const uncorrectAnswerSound = new Audio('uncorrect-answer.mp3')
const CurrentWord = (props) => {
    const [position, setPosition] = useState(0)
    const [isCorrect, setIsCorrect] = useState(false)

    const styles = {
        top:`${position}%`
    }

    const playUncorrectSound = () => {
        if (!props.isSoundOn) return
        uncorrectAnswerSound.currentTime = 0
        uncorrectAnswerSound.play()
    }

    useEffect(() => {
            setTimeout(() => {
                if(props.isCorrectAnswer) {
                    setIsCorrect(true)
                    if (position < 80) {
                        setPosition(position + 0.4)
                    } else {
                        props.correctChange()
                    }
                } else if(props.isUncorrectAnswer) {
                    setPosition(0)
                    props.uncorrectChange()
                } else if(position < 50) {
                    setPosition(position + 0.03)
                } else {
                    setPosition(0)
                    if(!isCorrect) {
                        playUncorrectSound()
                        props.attemptsChange()
                    }
                    setIsCorrect(false)
                    props.changeWords()
                }
            }, 0)
    })
    return props.isWordsLoad ? <p style={styles} className='current-word'>{props.word.word}</p> : ''
}

export default CurrentWord
