import React, {useState, useEffect} from 'react'
import './Statistics.scss'
import {Redirect} from "react-router-dom";
import ShortTermStatistics from "./ShortTermStatistics/ShortTermStatistics";
import LongTermStatistics from "./LongTermStatistics/LongTermStatistics";
import {useStore} from '../../store/store';

const Statistics = () => {
    const [sprintData, setSprintData] = useState(null)
    const [memoryData, setMemoryData] = useState(null)
    const [audioData, setAudioData] = useState(null)
    const [savannaData, setSavannaData] = useState(null)
    const [userLearnedWords, setUserLearnedWords] = useState(null)
    const [allDates, setAllDates]=useState(null)
    const [allWords, setAllWords]=useState(null)
    const [allWordsPeriod, setAllWordsPeriod]=useState(null)

    const userId = JSON.parse(localStorage.getItem("userData")) ? JSON.parse(localStorage.getItem("userData")).userId : null;
    if (userId === null) {
        return <Redirect to="/"/>
    }
    const token = JSON.parse(localStorage.getItem("userData")).token;

    const getStatistics = async ({userId}) => {
        const rawResponse = await fetch(`https://react-rslang-de.herokuapp.com/users/${userId}/statistics`, {
            method: 'GET',
            withCredentials: true,
            headers: {
                'Authorization': `Bearer ${token}`,
                'Accept': 'application/json',
            }
        });
        const content = await rawResponse.json();
        console.log('getStatistics', content.optional.sprintStatistics);
        console.log('getStatistics', content.optional.allDates);
        content.optional.sprintStatistics ? setSprintData(content.optional.sprintStatistics.sprint) : setSprintData(null)
        content.optional.memoryStatistics ? setMemoryData(content.optional.memoryStatistics.memory) : setMemoryData(null)
        // content.optional.audioStatistics?setAudioData(content.optional.audioStatistics.audio):setAudioData(null)
        // content.optional.savannaStatistics?setSavannaData(content.optional.savannaStatistics.savanna):setSavannaData(null)
        content.optional.learnedWords ? setUserLearnedWords(content.optional.learnedWords.learnedWords) : setUserLearnedWords(null)
        content.optional.allDates?setAllDates(content.optional.allDates.dates):setAllDates(null)
        content.optional.allWords?setAllWords(content.optional.allWords.words):setAllWords(null)
        content.optional.allWordsPeriod?setAllWordsPeriod(content.optional.allWordsPeriod.periodWords):setAllWordsPeriod(null)
    };

    return (
        <>
            <div className={'statistics_container'}>
                <h2>статистика</h2>
                <button className={'statistics_btn'} onClick={() => getStatistics({userId})}>Показать статистику
                </button>
                <ShortTermStatistics sprintData={sprintData} memoryData={memoryData} userLearnedWords={userLearnedWords}/>
                <LongTermStatistics allDates={allDates} allWords={allWords} allWordsPeriod={allWordsPeriod}/>
            </div>
        </>
    )
}

export default Statistics