import React from 'react'
import './LongTermStatistics.scss'
import { Bar } from 'react-chartjs-2';

const LongTermStatistics=(props)=>{
    return(
        <>
        <div className={'title_statistics'}>
            <h4>Изучено за весь период</h4>
            <div className={'chart_container'}>
                <Bar
                    data={{
                        labels:props.allDates,
                        datasets: [{
                            label: 'Изученные слова',
                            data: props.allWords,
                            backgroundColor: 'rgba(54, 162, 235, 1)',
                            borderColor: '#0f0fff',
                            borderWidth: 1
                        },
                            {
                                label:'Прирост изученных слов',
                                data:props.allWordsPeriod,
                                backgroundColor: ' #f6a91a',
                                borderColor: ' #f63a3a',
                                borderWidth: 1
                            }
                        ]
                    }}

                    options={{
                        maintainAspectRatio: false,
                        scales: {
                            yAxes: [{
                                ticks: {
                                    beginAtZero:true
                                }
                            }]
                        }
                    }}
                    height={400}
                />
            </div>

        </div>
        </>
    )
}
export default LongTermStatistics