import React, {useEffect} from 'react'
import './StatisticGameSprint.scss'

const StatisticGameSprint = (props) => {
    useEffect(() => {
        if (props.audioShowResult) {
            props.isSound ? props.audioShowResult.play() : props.audioShowResult.pause()
        }
        if (props.audioFinish) {
            props.audioFinish.pause()
        }
        if (props.setCounterRightAnswer) {
            props.setCounterRightAnswer(0)
        }
        if (props.setMatch) {
            props.setMatch([])
        }
        if (props.setCountFalse) {
            props.setCountFalse(0)
        }

    }, [])


    return (
        <div className={'statistic_container'}>
            <div className={'game_score'}>
                Твой результат:  {props.isResultSprint? props.countGame + ' баллов' :''}
            </div>
            <div className={'right_score'}>
                Правильные ответы:
                {props.isResultSprint ? ' ' +  props.countCorrect : ''}
            </div>

            <div className={'score_answer'}>
                {props.arrayCorrectAnswer.map((answer, index) => {
                    return (
                        <div className={'answer_container'} key={index}>
                            <div className={'btn_translate answer_btn'} onClick={() => {
                                const audio = new Audio(answer.dataWordsAudio)
                                audio.play()
                            }}>
                                <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 32 32">
                                    <path fill="currentColor"
                                          d="M15.788 13.007a3 3 0 110 5.985c.571 3.312 2.064 5.675 3.815 5.675 2.244 0 4.064-3.88 4.064-8.667 0-4.786-1.82-8.667-4.064-8.667-1.751 0-3.244 2.363-3.815 5.674zM19 26c-3.314 0-12-4.144-12-10S15.686 6 19 6s6 4.477 6 10-2.686 10-6 10z"
                                          fillRule="evenodd"></path>
                                </svg>
                            </div>
                            <div><span className={'english_answer'} onClick={() => {
                                const audio = new Audio(answer.dataWordsAudio)
                                audio.play()
                            }}>{answer.dataWords}</span> &mdash;  <span
                                className={'russian_answer'}>{answer.dataWordsTranslate}</span></div>
                        </div>)
                })}
            </div>

            <div className={'mistakes_score'}>
                Ошибки:
                {props.isResultSprint ? ' ' + props.countUnCorrect : ''}
            </div>
            <div className={'score_answer'}>
                {props.arrayUnCorrectAnswer.map((answer, index) => {
                    return (
                        <div className={'answer_container'} key={index}>
                            <div className={'btn_translate answer_btn'} onClick={() => {
                                const audio = new Audio(answer.dataWordsAudio)
                                audio.play()
                            }}>
                                <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 32 32">
                                    <path fill="currentColor"
                                          d="M15.788 13.007a3 3 0 110 5.985c.571 3.312 2.064 5.675 3.815 5.675 2.244 0 4.064-3.88 4.064-8.667 0-4.786-1.82-8.667-4.064-8.667-1.751 0-3.244 2.363-3.815 5.674zM19 26c-3.314 0-12-4.144-12-10S15.686 6 19 6s6 4.477 6 10-2.686 10-6 10z"
                                          fillRule="evenodd"></path>
                                </svg>
                            </div>
                            <div>
                            <span className={'english_answer'} onClick={() => {
                                const audio = new Audio(answer.dataWordsAudio)
                                audio.play()
                            }}>{answer.dataWords}</span> &mdash;  <span
                                className={'russian_answer'}>{answer.dataWordsTranslate}</span></div>
                        </div>)
                })}

            </div>
            <div>
                <div className={'link_start_game'} onClick={() => {
                    if (props.setIsVisibleStartCount) {
                        props.setIsVisibleStartCount(true)
                    }
                    if (props.setCountStart) {
                        props.setCountStart(4)
                    }
                    if (props.setIsVisibleBtnClose) {
                        props.setIsVisibleBtnClose(true)
                    }
                    if (props.setIsVisibleGameField) {
                        props.setIsVisibleGameField(false)
                    }
                }}>
                    Продолжить игру
                </div>

            </div>
        </div>
    )

}

export default StatisticGameSprint