import React, {useState, useEffect} from 'react'
import './SprintMain.scss'
import Countdown from "../Countdown/Countdown";
import GameField from "../GameField/GameField";
import {FullScreen, useFullScreenHandle} from "react-full-screen";
import FullScreenBtn from "../../FullScreenBtn/FullScreenBtn";


const SprintMain = (props) => {
    const [countStart, setCountStart] = useState(4)
    const [isVisibleStartCount, setIsVisibleStartCount] = useState(true)


    const audioFinish = new Audio('/audio/finish_tick.mp3')
    const handle = useFullScreenHandle();

    useEffect(() => {
        if (countStart > 0 && isVisibleStartCount) {
            setTimeout(() => {
                setCountStart(countStart - 1)
            }, 1000);
        } else {
            setIsVisibleStartCount(false)
            setCountStart(null)
        }
    }, [countStart])


    return (
        <div className={'game_container'}>
            <FullScreen handle={handle}>
            <div className={'mainSprint'}>
                <FullScreenBtn handle={handle}/>

                <div className={'container-game'}>
                    <div className={'game_box'}>
                        {(isVisibleStartCount) ?
                            (<div><Countdown countStart={countStart}/> <h2 className={'title_count'}>Подготовьтесь</h2>
                            </div>) :
                            (<GameField setIsVisibleStartCount={setIsVisibleStartCount}
                                        setCountStart={setCountStart} audioFinish={audioFinish}

                            />)}
                    </div>
                </div>
            </div>
            </FullScreen>
        </div>
    )
}
export default SprintMain