import React from 'react';
import {FontAwesomeIcon} from "@fortawesome/react-fontawesome";
import {faVolumeUp} from "@fortawesome/free-solid-svg-icons";
const VocabularyItem = ({item, index, sitePath}) => {
    const playAudio = (audio) =>{
        const mp3 = sitePath + audio
        const audioFile = new Audio(mp3)
        audioFile.play()
    }
    return(
        <div className='words-item' key={index}>
            <div>

            </div>

            <img width='280' src={sitePath + item.image} alt=""/>
            <p className='flex-start align-center'><b> {item.word}</b> {item.transcription}
                <FontAwesomeIcon

                    className='sound-icon'
                    icon={faVolumeUp}
                    onClick={()=> playAudio(item.audio)}
                />
            </p>
            <p dangerouslySetInnerHTML={{__html:item.textMeaning}}></p>
            <p>{item.textMeaningTranslate}</p>
            <hr/>
        </div>
    )
}
export default VocabularyItem;