import React, {useState, useEffect} from 'react'
import './GameContainer.scss'
import 'bootstrap/dist/css/bootstrap.min.css';
import Spinner from 'react-bootstrap/Spinner'

let indexWord = 0

function randomInteger(min, max) {
    let rand = min - 0.5 + Math.random() * (max - min + 1);
    return Math.round(+rand);
}

let indexWordTranslate = 0

function randomIntegerTranslate(min, max) {
    let rand = min - 0.5 + Math.random() * (max - min + 1);
    return Math.round(+rand);
}

const GameContainer = (props) => {
    const [dataWords, setDataWords] = useState(null)
    const [dataWordsTranslate, setDataWordsTranslate] = useState(null)
    const [dataWordsId, setDataWordsId] = useState(null)
    const [wordsAudio, setWordsAudio] = useState(null)
    const [dataWordsAudio, setDataWordsAudio] = useState(null)
    const [wordTranslate, setWordTranslate] = useState(null)
    const [wordTranslateId, setWordTranslateId] = useState(null)
    const [isCorrect, setIsCorrect] = useState(false)
    const [isUnCorrect, setIsUnCorrect] = useState(false)


    useEffect(() => {
        if (dataWords || dataWordsTranslate) {
            props.setIsLoader(false)
        }
    })
    let url
    if (props.isFirstLevel) {
        url = 'https://react-rslang-de.herokuapp.com/words?page=3&group=0'
    }
    if (props.isSecondLevel) {
        url = 'https://react-rslang-de.herokuapp.com/words?page=5&group=1'
    }
    if (props.isThirdLevel) {
        url = 'https://react-rslang-de.herokuapp.com/words?page=8&group=2'
    }
    if (props.isFourthLevel) {
        url = 'https://react-rslang-de.herokuapp.com/words?page=10&group=3'
    }
    if (props.isFifthLevel) {
        url = 'https://react-rslang-de.herokuapp.com/words?page=25&group=4'
    }
    if (props.isSixthLevel) {
        url = 'https://react-rslang-de.herokuapp.com/words?page=7&group=5'
    }


    useEffect(() => {
        fetch(url)
            .then(res => res.json())
            .then(data => {
                data[indexWord].word ? setDataWords(data[indexWord].word) : setDataWords(null)
                data[indexWord].wordTranslate ? setDataWordsTranslate(data[indexWord].wordTranslate) : setDataWords(null)
                data[indexWord].id ? setDataWordsId(data[indexWord].id) : setDataWords(null)
                data[indexWord].audio ? setWordsAudio((data[indexWord].audio)) : setWordsAudio(null)
                data[indexWordTranslate].wordTranslate ? setWordTranslate(data[indexWordTranslate].wordTranslate) : setDataWords(null)
                data[indexWordTranslate].id ? setWordTranslateId(data[indexWordTranslate].id) : setDataWords(null)
                let audio = data[indexWord].audio;
                fetch(`https://react-rslang-de.herokuapp.com/${audio}`)
                    .then((data) => {
                        data.url ? setDataWordsAudio(data.url) : setDataWordsAudio(null);
                    });
            })
    })

    useEffect(() => {
        props.isSound ? props.audioGong.play() : props.audioGong.pause()
    }, [])

    const audioTranslate = new Audio(dataWordsAudio)
    const correctAnswer = () => {

        if (dataWordsId === wordTranslateId) {
            if (!props.isFirstBonus && !props.isSecondBonus && !props.isThirdBonus) {
                props.setCountGame((prevCounter) => prevCounter + 10)
            } else if (props.isFirstBonus) {
                props.setCountGame((prevCounter) => prevCounter + 20)
            } else if (props.isSecondBonus) {
                props.setCountGame((prevCounter) => prevCounter + 40)
            } else if (props.isThirdBonus) {
                props.setCountGame((prevCounter) => prevCounter + 80)
            }

            props.setCountCorrect((prevCounter) => prevCounter + 1)
            props.isSound ? props.audioCorrect.play() : props.audioCorrect.pause()
            props.setArrayCorrectAnswer((prev => {
                return [...prev,
                    {
                        dataWords,
                        dataWordsTranslate,
                        dataWordsAudio
                    }]
            }))
            setIsCorrect(true)
            props.setCounterRightAnswer((prev) => prev + 1)


        } else {
            props.isSound ? props.audioFalse.play() : props.audioFalse.pause()
            props.setCountUnCorrect((prevCounter) => prevCounter + 1)
            props.setArrayUnCorrectAnswer((prev => {
                return [...prev,
                    {
                        dataWords,
                        dataWordsTranslate,
                        dataWordsAudio
                    }]
            }))
            setIsUnCorrect(true)
            props.setCounterRightAnswer(0)

        }
    }

    useEffect(() => {
        let timer
        if (isCorrect) {
            timer = setTimeout(() => {
                setIsCorrect(false)
            }, 500)
        }
        return () => {
            clearTimeout(timer)
        }
    }, [isCorrect])

    useEffect(() => {
        let timer
        if (isUnCorrect) {
            timer = setTimeout(() => {
                setIsUnCorrect(false)
            }, 500)
        }
        return () => {
            clearTimeout(timer)
        }
    }, [isUnCorrect])

    const unCorrectAnswer = () => {
        if (dataWordsId === wordTranslateId) {
            props.setCountUnCorrect((prevCounter) => prevCounter + 1)
            props.isSound ? props.audioFalse.play() : props.audioFalse.pause()
            props.setArrayUnCorrectAnswer((prev => {
                return [...prev,
                    {
                        dataWords,
                        dataWordsTranslate,
                        dataWordsAudio
                    }]
            }))
            setIsCorrect(false)
            props.setCounterRightAnswer(0)
        } else {

            if (!props.isFirstBonus && !props.isSecondBonus && !props.isThirdBonus) {
                props.setCountGame((prevCounter) => prevCounter + 10)
            } else if (props.isFirstBonus) {
                props.setCountGame((prevCounter) => prevCounter + 20)
            } else if (props.isSecondBonus) {
                props.setCountGame((prevCounter) => prevCounter + 40)
            } else if (props.isThirdBonus) {
                props.setCountGame((prevCounter) => prevCounter + 80)
            }
            props.setCountCorrect((prevCounter) => prevCounter + 1)
            props.isSound ? props.audioCorrect.play() : props.audioCorrect.pause()
            props.setArrayCorrectAnswer((prev => {
                return [
                    ...prev,
                    {
                        dataWords,
                        dataWordsTranslate,
                        dataWordsAudio
                    }]
            }))
            setIsCorrect(true)
            props.setCounterRightAnswer((prev) => prev + 1)
        }

    }
    useEffect(() => {
        if (props.counterRightAnswer === 1 || props.counterRightAnswer === 5 || props.counterRightAnswer === 9) {
            props.setFirstPoint(true)
        }
        if (props.counterRightAnswer === 2 || props.counterRightAnswer === 6 || props.counterRightAnswer === 10) {
            props.setSecondPoint(true)
        }
        if (props.counterRightAnswer === 3 || props.counterRightAnswer === 7 || props.counterRightAnswer === 11) {
            props.setThirdPoint(true)
        }
        if (props.counterRightAnswer === 0 || props.counterRightAnswer === 4 || props.counterRightAnswer === 8) {
            props.setFirstPoint(false)
            props.setSecondPoint(false)
            props.setThirdPoint(false)

        }
        if (props.counterRightAnswer === 4) {
            props.setFirstBonus(true)
            props.setFirstParrot(true)

        }
        if (props.counterRightAnswer === 8) {
            props.setFirstBonus(false)
            props.setSecondBonus(true)
            props.setSecondParrot(true)
        }
        if (props.counterRightAnswer === 12) {
            props.setFirstBonus(false)
            props.setSecondBonus(false)
            props.setThirdBonus(true)
            props.setThirdParrot(true)
        }
        if (props.counterRightAnswer === 0) {
            props.setFirstPoint(false)
            props.setSecondPoint(false)
            props.setThirdPoint(false)
            props.setFirstBonus(false)
            props.setSecondBonus(false)
            props.setThirdBonus(false)
            props.setFirstParrot(false)
            props.setSecondParrot(false)
            props.setThirdParrot(false)
        }

    })
    // console.log('isCorrect,isUnCorrect')
    // console.log(isCorrect,isUnCorrect)

    return (
        <div
            className={`parrot_field ${isCorrect ? 'parrot_field_true' : ''} ${isUnCorrect ? 'parrot_field_false' : ''}`}>
            <div className={'btn_translate'} onClick={() => audioTranslate.play()}>
                <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 32 32">
                    <path fill="currentColor"
                          d="M15.788 13.007a3 3 0 110 5.985c.571 3.312 2.064 5.675 3.815 5.675 2.244 0 4.064-3.88 4.064-8.667 0-4.786-1.82-8.667-4.064-8.667-1.751 0-3.244 2.363-3.815 5.674zM19 26c-3.314 0-12-4.144-12-10S15.686 6 19 6s6 4.477 6 10-2.686 10-6 10z"
                          fillRule="evenodd"></path>
                </svg>
            </div>
            <div className={'points'}>
                <div className={'point'}>
                    {props.isFirstPoint &&
                    <img
                        src={'data:image/svg+xml;base64,'}
                        alt={'point'}/>}
                </div>
                <div className={'point'}>
                    {props.isSecondPoint && <img
                        src={'data:image/svg+xml;base64,'}
                        alt={'point'}/>}
                </div>
                <div className={'point'}>
                    {props.isThirdPoint && <img
                        src={'data:image/svg+xml;base64,'}
                        alt={'point'}/>}
                </div>
            </div>
            <div className={'bonus_container'}>
                {props.isFirstBonus && <p className={'bonus_score'}>+20 очков за слова</p>}
                {props.isSecondBonus && <p className={'bonus_score'}>+40 очков за слова</p>}
                {props.isThirdBonus && <p className={'bonus_score'}>+80 очков за слова</p>}
            </div>

            <div className={'parrots_container'}>
                <div className={'parrots'}>
                    <img
                        src={'data:image/svg+xml;base64,'}
                        alt={'parrot'}/>
                    {props.isFirstParrot && <img
                        src={'data:image/svg+xml;base64,'}
                        alt={'parrot'}/>}
                    {props.isSecondParrot && <img
                        src={'data:image/svg+xml;base64,'}
                        alt={'parrot'}/>}
                    {props.isThirdParrot && <img
                        src={'data:image/svg+xml;base64,'}
                        alt={'parrot'}/>}
                </div>
                <img className={'branch'}
                     src={'data:image/svg+xml;base64,'}
                     alt={'branch'}/>
            </div>
            <div className={'word_container'}>
                {(props.isLoader) ? (<Spinner animation="border" variant="danger"/>) : (
                    <>
                        <div className={'english_word'}>{dataWords}</div>
                        <div className={'russian_word'}>{wordTranslate}</div>
                    </>
                )}

            </div>
            <div className={'button_container'}>
                <button onClick={() => {
                    indexWord = randomInteger(0, 19)
                    indexWordTranslate = randomIntegerTranslate(0, 19)
                    unCorrectAnswer()
                }}>Неверно
                </button>
                <button onClick={() => {
                    indexWord = randomInteger(0, 19)
                    indexWordTranslate = randomIntegerTranslate(0, 19)
                    correctAnswer()
                }}>Верно
                </button>
            </div>
        </div>
    )
}
export default GameContainer