import React, {useState, useContext, useEffect} from 'react'
import {Redirect} from "react-router-dom";

const SprintStatisticsContext = React.createContext()
const MemoryStatisticsContext = React.createContext()
const AudioStatisticsContext = React.createContext()
const SavannaStatisticsContext = React.createContext()

export const useSprintStatistics = () => {
    return useContext(SprintStatisticsContext)
}
export const useMemoryStatistics = () => {
    return useContext(MemoryStatisticsContext)
}
export const useAudioStatistics = () => {
    return useContext(MemoryStatisticsContext)
}
export const useSavannaStatistics = () => {
    return useContext(MemoryStatisticsContext)
}


const StatisticsProvider = ({children}) => {
    const [sprintStatistics, setSprintStatistics] = useState({})
    const [memoryStatistics, setMemoryStatistics] = useState({})
    const [audioStatistics, setAudioStatistics] = useState({})
    const [savannaStatistics, setSavannaStatistics] = useState({})
    const [learnedWords, setLearnedWords] = useState({})
    const [dates, setDates] = useState([])
    const [userWords, setUserWords] = useState([])
    const [userWordsPeriod, setUserWordsPeriod] = useState([])


    const getDataSprint = (value) => {
        setSprintStatistics(value)
    }
    const getDataMemory = (value) => {
        setMemoryStatistics(value)
    }
    const getDataAudio = (value) => {
        setAudioStatistics(value)
    }
    const getDataSavanna = (value) => {
        setSavannaStatistics(value)
    }

    useEffect(()=>{
        const userId = JSON.parse(localStorage.getItem("userData")) ? JSON.parse(localStorage.getItem("userData")).userId : null;
        if (userId === null) {
            return <Redirect to="/"/>
        }
        const token = JSON.parse(localStorage.getItem("userData")).token;
        const getWords = async ({userId}) => {
            const rawResponse = await fetch(`https://react-rslang-de.herokuapp.com/users/${userId}/aggregatedWords`, {
                method: 'GET',
                withCredentials: true,
                headers: {
                    'Authorization': `Bearer ${token}`,
                    'Accept': 'application/json',
                    'Content-Type': 'application/json'
                },
            });
            const content = await rawResponse.json();
            let  words = content[0].paginatedResults.length
            let dateToday=new Date().toLocaleDateString()
            if(words!==0){
                setLearnedWords({"learnedWords":words,
                    "dateToday":dateToday})
            }
        };
        getWords({userId})
    },[])


    let dateToday = new Date().toLocaleDateString()
    useEffect(() => {
        if (learnedWords.dateToday !== dateToday) {
            setSprintStatistics({})
            setMemoryStatistics({})
            setAudioStatistics({})
            setSavannaStatistics({})
            setLearnedWords({})
        }
    }, [])

    useEffect(() => {
        let countWords = learnedWords.learnedWords
        if (learnedWords.learnedWords||userWords.length===0||userWordsPeriod.length===0) {

            setUserWords((prev) => {
                return [countWords]
            })
            setUserWordsPeriod((prev) => {
                return [countWords]
            })
        }
        console.log("userWords", userWords)
    }, [learnedWords])

    useEffect(() => {
        if (!dates.includes(dateToday)) {
            setDates((prev) => {
                return [...prev, dateToday]
            })
            let countWords = learnedWords.learnedWords
            if(learnedWords.learnedWords){
                setUserWords((prev) => {
                    return [...prev, countWords-prev]
                })
                let periodWords=(userWords[userWords.length-1]-userWords[userWords.length-2])+userWordsPeriod[userWordsPeriod.length-1]
                setUserWordsPeriod((prev)=>{
                    return[...prev,periodWords]
                })
            }
        }
    }, [])

    let dataStatistics
    let allDates={ "dates":dates}
    let allWords={"words":userWords}
    let allWordsPeriod={"periodWords":userWordsPeriod}
    if (learnedWords || sprintStatistics || memoryStatistics || audioStatistics || savannaStatistics) {
        dataStatistics = {
            "optional":
                {
                    learnedWords, sprintStatistics, memoryStatistics, audioStatistics, savannaStatistics,allDates,allWords,allWordsPeriod
                }
        }
    }
    console.log('dataStatistics', dataStatistics)


    const userId = JSON.parse(localStorage.getItem("userData")) ? JSON.parse(localStorage.getItem("userData")).userId : null;
    if (userId === null) {
        return <Redirect to="/"/>
    }
    const token = JSON.parse(localStorage.getItem("userData")).token;
    const createStatistics = async ({userId, dataStatistics}) => {
        const rawResponse = await fetch(`https://react-rslang-de.herokuapp.com/users/${userId}/statistics`, {
            method: 'PUT',
            withCredentials: true,
            headers: {
                'Authorization': `Bearer ${token}`,
                'Accept': 'application/json',
                'Content-Type': 'application/json'
            },
            body: JSON.stringify(dataStatistics)
        });
        const content = await rawResponse.json();
        console.log('createStatistics', content);
    };
    createStatistics({userId, dataStatistics})

console.log("learnedWords",learnedWords)
    return (
        <SprintStatisticsContext.Provider value={getDataSprint}>
            <MemoryStatisticsContext.Provider value={getDataMemory}>
                <AudioStatisticsContext.Provider value={getDataAudio}>
                    <SavannaStatisticsContext.Provider value={getDataSavanna}>
                            {children}
                    </SavannaStatisticsContext.Provider>
                </AudioStatisticsContext.Provider>
            </MemoryStatisticsContext.Provider>
        </SprintStatisticsContext.Provider>
    )
}
export default StatisticsProvider