import React from 'react'
import {BrowserRouter as Router, Switch, Route, Redirect} from "react-router-dom";

import './App.scss';
import Header from "./components/Header/Header";
import Footer from "./components/Footer/Footer";
import Home from "./pages/Home/Home";
import Profile from "./pages/Profile/Profile";

import MemoryMain from "./components/MemoryGame/MemoryMain/MemoryMain";
import SprintMain from './components/SprintGame/SprintMain/SprintMain'


function App() {
    const userId = JSON.parse(localStorage.getItem("userData")) ? JSON.parse(localStorage.getItem("userData")).userId : null;

    return (
        <div className="App">
            <Router>
                {userId === null &&
                <Redirect  to="/" />
                }
                <Header/>
                <main>
                      <Switch>
                          <Route path="/" exact>
                              <Home/>
                          </Route>
                          <Route path="/profile" >
                              <Profile />
                          </Route>
                      </Switch>
                </main>
                <Footer/>
            </Router>
        </div>
    )
}

export default App;
