import React, {useState, useEffect} from 'react'
import './MemoryContainer.scss'
import 'bootstrap/dist/css/bootstrap.min.css';
import Spinner from 'react-bootstrap/Spinner'
import {useMemoryStatistics} from "../../Statistics/StatisticsContext";

const MemoryContainer = (props) => {
    const [countRight, setCountRight] = useState(0)
    const [countAnswer, setCountAnswer] = useState([])
    //statistics provider
    const setMemoryStatistics = useMemoryStatistics()
    useEffect(() => {
        setCountAnswer(pref => {
            return [
                ...pref,
                countRight
            ]
        })
        // console.log('CountAnswer', countAnswer)
    }, [countRight])

    function getMaxOfArray(countAnswer) {
        return Math.max.apply(null, countAnswer);
    }
    let learnedWords = props.arrayCorrectAnswer.length
    let rightWords = Math.round((props.arrayCorrectAnswer.length * 100) / (props.arrayCorrectAnswer.length + props.arrayUnCorrectAnswer.length))
    let longCorrectAnswer = getMaxOfArray(countAnswer)
    let arrayStatistics = {
        "memory": {
            "learnedWords": learnedWords,
            "rightWords": rightWords,
            "longCorrectAnswer": longCorrectAnswer
        }
    }
    // console.log(learnedWords,rightWords,longCorrectAnswer)


    useEffect(() => {
        if (props.dataEnWords && props.dataRuWords) {
            const firstMatch = props.dataEnWords[props.clickingCardEn[0]]
            const secondMatch = props.dataRuWords[props.clickingCardRu[0]]
            if (secondMatch && firstMatch && firstMatch.id === secondMatch.id) {
                const audioCorrect = new Audio('/audio/trueAnswer.mp3')
                setCountRight((prev)=>prev+1)
                props.isSound ? audioCorrect.play() : audioCorrect.pause()
                props.setMatch((prev)=>[...prev,firstMatch.id])
                let dataWords = firstMatch.word;
                let dataWordsTranslate = firstMatch.wordTranslate;
                let dataWordsAudio = `https://react-rslang-de.herokuapp.com/${firstMatch.audio}`;
                props.setArrayCorrectAnswer((prev) => {
                    return [
                        ...prev,
                        {
                            dataWords,
                            dataWordsTranslate,
                            dataWordsAudio
                        }
                    ]
                })
            } else if (secondMatch && firstMatch && firstMatch.id !== secondMatch.id) {
                const audioUnCorrect = new Audio('/audio/falseAnswer.mp3')
                props.isSound ? audioUnCorrect.play() : audioUnCorrect.pause()
                props.setCountFalse((prev)=>prev+1)
                setCountRight(0)
                let dataWords = firstMatch.word;
                let dataWordsTranslate = firstMatch.wordTranslate;
                let dataWordsAudio = `https://react-rslang-de.herokuapp.com/${firstMatch.audio}`;
                props.setArrayUnCorrectAnswer((prev) => {
                    return [
                        ...prev,
                        {
                            dataWords,
                            dataWordsTranslate,
                            dataWordsAudio
                        }
                    ]
                })
            }
        }
    }, [props.clickingCardEn])
    useEffect(() => {
        if (props.dataEnWords && props.dataRuWords) {
            const firstMatch = props.dataEnWords[props.clickingCardEn[0]]
            const secondMatch = props.dataRuWords[props.clickingCardRu[0]]
            if (secondMatch && firstMatch && firstMatch.id === secondMatch.id) {
                const audioCorrect = new Audio('/audio/trueAnswer.mp3')
                props.isSound ? audioCorrect.play() : audioCorrect.pause()
               setCountRight((prev)=>prev+1)
                props.setMatch((prev)=>[...prev,firstMatch.id])
                let dataWords = firstMatch.word;
                let dataWordsTranslate = firstMatch.wordTranslate;
                let dataWordsAudio = `https://react-rslang-de.herokuapp.com/${firstMatch.audio}`;
                props.setArrayCorrectAnswer((prev) => {
                    return [
                        ...prev,
                        {
                            dataWords,
                            dataWordsTranslate,
                            dataWordsAudio
                        }
                    ]
                })
            } else if (secondMatch && firstMatch && firstMatch.id !== secondMatch.id) {
                const audioUnCorrect = new Audio('/audio/falseAnswer.mp3')
                props.isSound ? audioUnCorrect.play() : audioUnCorrect.pause()
                props.setCountFalse((prev)=>prev+1)
               setCountRight(0)
                let dataWords = firstMatch.word;
                let dataWordsTranslate = firstMatch.wordTranslate;
                let dataWordsAudio = `https://react-rslang-de.herokuapp.com/${firstMatch.audio}`;
                props.setArrayUnCorrectAnswer((prev) => {
                    return [
                        ...prev,
                        {
                            dataWords,
                            dataWordsTranslate,
                            dataWordsAudio
                        }
                    ]
                })
            }
        }
    },  [props.clickingCardRu])

    useEffect(() => {
        let timer
        if (props.clickingCardEn.length === 2) {
            props.setClickingCardEn([])
        }
        if (props.clickingCardEn.length === 1 && props.clickingCardRu.length === 1) {
            timer = setTimeout(() => {
                props.setClickingCardEn([])
                props.setClickingCardRu([])
            }, 300)
        }
        return () => {
            clearTimeout(timer)
        }
    }, [props.clickingCardEn])

    useEffect(() => {
        let timer
        if (props.clickingCardRu.length === 2) {
            props.setClickingCardRu([])
        }
        if (props.clickingCardEn.length === 1 && props.clickingCardRu.length === 1) {
            timer = setTimeout(() => {
                props.setClickingCardEn([])
                props.setClickingCardRu([])
            }, 300)
        }
        return () => {
            clearTimeout(timer)
        }
    }, [props.clickingCardRu])

    const handleClickEn = (index) => {

        props.setClickingCardEn((prev) => [...prev, index])
    }
    const handleClickRu = (index) => {

        props.setClickingCardRu((prev) => [...prev, index])
    }

    let copyMathch=props.match.slice()
    let matchCard = [...new Set(copyMathch)]
    if (matchCard.length === 10) {
        props.setIsVisibleGameField(false)
    }
    useEffect(() => {
        if ( props.countFalse === 1) {
            props.setIsFirstLive(false)
        }
    })
    useEffect(() => {
        if ( props.countFalse === 2) {
            props.setIsSecondLive(false)
        }
    })
    useEffect(() => {
        if ( props.countFalse === 3) {
            props.setIsThirdLive(false)
            props.setIsVisibleGameField(false)
            props.setCountFalse(0)
            props.setClickingCardEn([])
            props.setClickingCardRu([])
            props.setMatch([])
            setMemoryStatistics(arrayStatistics)
        }
    })

    return (
        <div className={'cards_field'}>
            <div className={'english_words'}>
                {(props.isLoader)?( <Spinner animation="border" variant="danger" />):(
                    <>
                    {props.dataEnWords && props.dataEnWords.map((value, index) => {
                            let isDisableCard = false
                            let isFalseCard = false
                            let isActiveCard = false
                            if (props.clickingCardEn.includes(index)) {
                                isActiveCard = true
                                isFalseCard = true
                            }
                            if (props.match.includes(value.id)) {
                                isDisableCard = true
                            }
                            return (
                                <div id={value.id} key={value.id} data={value.wordTranslate}
                                     className={`card_word ${isActiveCard ? 'active' : ''} ${isFalseCard ? 'false' : ''} ${isDisableCard ? 'disable' : ''}`}
                                     check={'false'}
                                     onClick={() => {
                                         handleClickEn(index)
                                     }}>
                                    <audio src={`https://react-rslang-de.herokuapp.com/${value.audio}`}/>
                                    {value.word}</div>
                            )
                        })}
                    </>
                )}

            </div>
            <div className={'russian_words'}>
                {(props.isLoader)?( <Spinner animation="border" variant="danger" />):(
                    <>
                    {props.dataRuWords && props.dataRuWords.map((value, index) => {
                            let isDisableCard = false
                            let isFalseCard = false
                            let isActiveCard = false
                            if (props.clickingCardRu.includes(index)) {
                                isActiveCard = true
                                isFalseCard = true
                            }
                            if (props.match.includes(value.id)) {
                                isDisableCard = true
                            }
                            return (
                                <div id={value.id} key={value.id}
                                     className={`card_word ${isActiveCard ? 'active' : ''} ${isFalseCard ? 'false' : ''} ${isDisableCard ? 'disable' : ''}`}
                                     check={'false'}
                                     onClick={() => {
                                         handleClickRu(index)
                                     }}>{value.wordTranslate}</div>
                            )
                        })}
                    </>
                )}

            </div>
        </div>
    )
}
export default MemoryContainer