import React from 'react';
import './Games.scss'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faPuzzlePiece} from '@fortawesome/free-solid-svg-icons'
import GamePreview from "./GamePreview";
import SprintMain from "../SprintGame/SprintMain/SprintMain";
import AudioCallMain from "../AudioCallGame/AudioCallMain/AudioCallMain"
import MemoryMain from '../MemoryGame/MemoryMain/MemoryMain'

import {Route, Switch} from "react-router-dom";
import Education from "../Education/Education";
import SavannahGame from "../savannahGame/savannahGame/SavannahGame";
import MainScreen from "../savannahGame/mainScreen/MainScreen";

const Games = () =>{
    return(
        <div>
            <h1>Игры</h1>
            <div className="games__wrapper flex-start flex-wrap ">
                <Switch>
                    <Route path="/profile/games" exact>
                        <GamePreview route='sprint' gameName='Спринт'/>
                        <GamePreview route='audiocall' gameName='Аудиовызов'/>
                        <GamePreview route='savannah-head'  gameName='Саванна'/>
                        <GamePreview route='memory' gameName='Memory'/>
                    </Route>
                    <Route path="/profile/games/sprint" >
                        <SprintMain/>
                    </Route>
                    <Route path="/profile/games/audiocall" >
                        <AudioCallMain/>
                    </Route>
                    <Route path="/profile/games/memory" >
                        <MemoryMain/>
                    </Route>
                    <Route path="/profile/games/savannah-head" >
                        <MainScreen/>
                    </Route>
                    <Route path="/profile/games/savannah" >
                        <SavannahGame/>
                    </Route>
                </Switch>


            </div>

        </div>
    )
}
export default Games;