import './WordsField.scss'

const WordsField = (props) => {
    const words = props.otherWords.concat(props.trueWord)
    const wordsElements = props.isWordsLoad ? words.map(
        elem => <p className='words-field__word'
                   onClick={() => props.clickedWord(elem)}>{elem.wordTranslate}</p>
    ) : []
    return <div className='words-field'>
        { wordsElements.sort(() => Math.random() - 0.5)}
    </div>
}

export default WordsField
