const SHOW_TRANSLATE_MEANING = 'SHOW_TRANSLATE_MEANING';
const SHOW_TRANSLATE_WORD = 'SHOW_TRANSLATE_WORD';
const SHOW_DIFF_DELETE_BTNS = 'SHOW_DIFF_DELETE_BTNS';

function noop(){

}
export const initialState = {

    settings: {
        showTranslateMeaning: true,
        showTranslateWord: true,
        showDeleteDiffWords: true
    },
    sitePath: 'https://react-rslang-de.herokuapp.com/'
};

export const showTranslateMeaningAC = () => ({
    type: SHOW_TRANSLATE_MEANING
});

export const showTranslateWordAC = () => ({
    type: SHOW_TRANSLATE_WORD
});

export const showDiffDeleteWordsAC = () => ({
    type: SHOW_DIFF_DELETE_BTNS
});
export const mainReducer = (state = initialState, action) => {
    if (action.type === SHOW_TRANSLATE_MEANING) {

        return {
            ...state,
            settings:{
                ...state.settings,
                showTranslateMeaning: !state.settings.showTranslateMeaning
            }
        };

    } else if(action.type === SHOW_TRANSLATE_WORD){
        return {
            ...state,
            settings:{
                ...state.settings,
                showTranslateWord: !state.settings.showTranslateWord
            }
        };
    }
    else if(action.type === SHOW_DIFF_DELETE_BTNS){
        return {
            ...state,
            settings:{
                ...state.settings,
                showDeleteDiffWords: !state.settings.showDeleteDiffWords
            }
        };
    }
};