import React from 'react';
import './Settings.scss'

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faCheckDouble} from '@fortawesome/free-solid-svg-icons'
import { useStore } from '../../store/store';
import {showTranslateMeaningAC, showDiffDeleteWordsAC, showTranslateWordAC} from '../../store/mainReducer';

const Settings = () => {
    const [, dispatch] = useStore();
    const [state] = useStore();

    const changeTranslateMeaning = () =>{
       dispatch(showTranslateMeaningAC());
    }

    const changeTranslateWorld = () =>{
        dispatch(showTranslateWordAC());
    }
    const changeDiffDeleteWords= () =>{
        dispatch(showDiffDeleteWordsAC());
    }
    return(
        <div className='settings'>
            <h1>Настройки</h1>
            <div className="settings__item align-center flex-start">
                <div className='check'>
                    <input checked={state.settings.showTranslateWord} onChange={changeTranslateWorld} id='settings2' type="checkbox"/>
                    <span>
                        <FontAwesomeIcon icon={faCheckDouble}/>
                    </span>
                </div>

                <label htmlFor="settings2">Отображать перевод слова</label>
            </div>
            <div className="settings__item align-center flex-start">
                <div className='check'>
                    <input checked={state.settings.showTranslateMeaning} onChange={changeTranslateMeaning} id='settings1' type="checkbox"/>
                    <span>
                        <FontAwesomeIcon icon={faCheckDouble}/>
                    </span>
                </div>

                <label htmlFor="settings1">Отображать значение перевода</label>
            </div>
            <div className="settings__item align-center flex-start">
                <div className='check'>
                    <input checked={state.settings.showDeleteDiffWords} onChange={changeDiffDeleteWords} id='settings3' type="checkbox"/>
                    <span>
                        <FontAwesomeIcon icon={faCheckDouble}/>
                    </span>
                </div>

                <label htmlFor="settings3">Добавлять слова в Сложные и Удаленные</label>
            </div>
        </div>
    )
}
export default Settings;