import React from 'react'
import {FontAwesomeIcon} from '@fortawesome/react-fontawesome'
import {faGithub} from '@fortawesome/free-brands-svg-icons'
import './Team.scss'

const Team = () => {
    return (
        <div className={'team_container'}>
            <h2>наша команда</h2>
            <div className={'team_card'}>
                <img src={'https://cdn.iconscout.com/icon/free/png-512/laptop-user-1-1179329.png'} alt={'team'}
                     width={200}/>
                <div className={'container_about'}>
                    <div className={'title'}>
                        <h3>Vadim Velikanets</h3>
                        <a href={'https://github.com/VadimVelikanets'} title={'github'}> <FontAwesomeIcon
                            icon={faGithub}/></a>
                    </div>
                    <h5>Team Lead</h5>
                    <p>Реализовал игру "Аудиовызов", верстку и дизайн главной старницы. Разработал разделы обучение,
                        словарь, настройки </p>
                </div>

            </div>
            <div className={'team_card'}>
                <img src={'https://cdn4.iconfinder.com/data/icons/profession-avatar-5/64/29-programmer-512.png'}
                     alt={'team'} width={200}/>
                <div className={'container_about'}>
                    <div className={'title'}>
                        <h3>Pavel Ptashynski</h3>
                        <a href={'https://github.com/ebces'} title={'github'}> <FontAwesomeIcon icon={faGithub}/></a>
                    </div>
                    <p>Реализовал игру "Саванна"</p>
                </div>

            </div>
            <div className={'team_card'}>
                <img
                    src={'https://www.vippng.com/png/detail/7-78563_computer-icons-user-laptop-personal-computer-girl-typing.png'}
                    alt={'team'} width={200}/>
                <div className={'container_about'}>
                    <div className={'title'}>
                        <h3>Olga Martinchik</h3>
                        <a href={'https://github.com/olgamartinchik'} title={'github'}> <FontAwesomeIcon
                            icon={faGithub}/></a>
                    </div>
                    <p>Реализовала игры "Спринт", "Своя игра", раздел Статистика</p>

                </div>

            </div>
        </div>
    )
}
export default Team