import React, {useEffect, useState} from 'react'
import './FullScreenBtn.scss'
import {FontAwesomeIcon} from "@fortawesome/react-fontawesome";
import {faExpand} from '@fortawesome/free-solid-svg-icons';
import {faCompress} from '@fortawesome/free-solid-svg-icons';

const FullScreenBtn = (props) => {
    const [isClickingBtn, setIsClickingBtn] = useState(true)

    const handleClickFullScreen = () => {
        setIsClickingBtn(isClickingBtn === false ? true : false)
        if (isClickingBtn) {
            props.handle.enter()
        } else {
            props.handle.exit()
        }
    }
useEffect(()=>{
    document.addEventListener('fullscreenchange',  (e) =>{
        if (document.fullscreenElement){
            setIsClickingBtn(false)
        }else{
            setIsClickingBtn(true)
        }
    });
},[])

    // console.log('isClickingBtn', isClickingBtn)

    return (
        <>
            <div className={'fullScreen_btn'} onClick={handleClickFullScreen}>
                {isClickingBtn ? <FontAwesomeIcon icon={faExpand}/> : <FontAwesomeIcon icon={faCompress}/>}
            </div>
        </>
    )
}
export default FullScreenBtn