import React, {useState} from 'react';
import {Link} from "react-router-dom";
import './Sidebar.scss';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import {faBars, faBook, faGamepad, faSlidersH, faUserFriends, faTable, faGraduationCap, faTimes} from '@fortawesome/free-solid-svg-icons'

const Sidebar = () =>{
    const [isMenuOpen, setMenuOpen] = useState(false)
    const openMenuHandler = () => {
        setMenuOpen(!isMenuOpen)
    }
    return(

        <div
            className={`sidebar ${isMenuOpen ? "active" : ""}`}
        >
            {isMenuOpen ?
                <FontAwesomeIcon  icon={faTimes}  onClick={openMenuHandler}/>
                : <FontAwesomeIcon  icon={faBars}  onClick={openMenuHandler}/>
            }

            <ul>
                <li className='flex-start align-center'>
                    <FontAwesomeIcon icon={faGraduationCap}/>
                    <Link to="/profile">Обучение</Link>
                </li>
                <li className='flex-start align-center'>
                    <FontAwesomeIcon icon={faBook}/>
                    <Link to="/profile/vocabulary">Словарь</Link>
                </li>
                <li className='flex-start align-center'>
                    <FontAwesomeIcon icon={faGamepad}/>
                    <Link to="/profile/games">Игры</Link>
                </li>

                <li className='flex-start align-center'>
                    <FontAwesomeIcon icon={faTable}/>
                    <Link to="/profile/statistics">Стратистика</Link>

                </li>
                <li className='flex-start align-center'>
                    <FontAwesomeIcon icon={faSlidersH}/>
                    <Link to="/profile/settings">Настройки</Link>
                </li>
                <li className='flex-start align-center'>
                    <FontAwesomeIcon icon={faUserFriends}/>
                    <Link to="/profile/team">Команда</Link>
                </li>
            </ul>
        </div>
    )
}

export default Sidebar;