import React from 'react';
import './Home.scss'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faFlagCheckered} from '@fortawesome/free-solid-svg-icons'
import Team from '../../components/Team/Team'
import Sidebar from "../../components/Sidebar/Sidebar";
const Home = (props) =>{
    return(
        <div className='home-page' >
            <Sidebar/>
            <div className="head-block" style={{ backgroundImage: "url('/img/bg2.png'),url('/img/bg1.png')" }}>
                <div className="container">
                    <h1>Онлайн обучение английскому языку</h1>
                    <p>Освойте английский язык с нуля благодаря нашему инновационному приложению, включающему в себя устные и письменное задания, а также онлайн-игры.</p>
                </div>

            </div>
            <div className="container">
                <Team/>
            </div>
            <div className="head-block" style={{ backgroundImage: "url('/img/bg2.png'),url('/img/bg1.png')" }}>
                <div className="container">
                    <h1>Наши преимущества</h1>
                    <ul className='advantages'>
                        <li>
                            <FontAwesomeIcon icon={faFlagCheckered}/>
                            <span>
                               Обучение в формате игры
                            </span>
                        </li>
                        <li>
                            <FontAwesomeIcon icon={faFlagCheckered}/>
                            <span>
                               Использование аудио-упражнений
                            </span>
                        </li>
                        <li>
                            <FontAwesomeIcon icon={faFlagCheckered}/>
                            <span>
                               Гибкая система настроек
                            </span>
                        </li>
                        <li>
                            <FontAwesomeIcon icon={faFlagCheckered}/>
                            <span>
                              Огромная база слов и упражнений
                            </span>
                        </li>
                    </ul>

                </div>

            </div>
            <div className="info-block">
                <div className="container">
                    <h2>Как пользоваться этим чудестным приложением?</h2>
                    <p>Посмотрите короткий обучающий ролик о том как пользоваться нашим приложением</p>
                    <iframe width="560" height="315" src="https://www.youtube.com/embed/OEHSGZrIYGE"
                            title="YouTube video player" frameBorder="0"
                            allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
                            allowFullScreen></iframe>
                </div>
            </div>
        </div>
    )
}

export default Home;