import React, {useState} from "react"
import './MemoryMain.scss'
import MemoryField from "../MemoryField/MemoryField";
import {FullScreen, useFullScreenHandle} from "react-full-screen";
import FullScreenBtn from "../../FullScreenBtn/FullScreenBtn";


const MemoryMain = () => {
    const [isVisibleGameField, setIsVisibleGameField] = useState(false)

    const handle = useFullScreenHandle();
    const audioFinish = new Audio('/audio/finish_tick.mp3')
    return (
        <div className={'game_container'}>
            <FullScreen handle={handle}>
                <div className='mainMemory'>
                    <FullScreenBtn handle={handle}/>
                    <div className={'container-game'}>
                        <div className={'game_box'}>
                            {isVisibleGameField ? (
                                    <MemoryField setIsVisibleGameField={setIsVisibleGameField}
                                                 audioFinish={audioFinish}
                                    />) :
                                (<div className={'start_box'}>
                                        <div className={'regulations'}>
                                            <div className={'regulations_container'}>
                                                <p className={'about_game'}>Кликай по словам,<br/> чтобы совместить
                                                    слово и
                                                    его
                                                    перевод. <br/> Игра помогает улучшить запоминание слов.</p>
                                            </div>
                                        </div>
                                        <div className={'container_btn'}>
                                            <button className={'start_game'} onClick={() => {
                                                setIsVisibleGameField(true)
                                            }}>тренировать
                                            </button>
                                        </div>
                                    </div>
                                )}
                        </div>
                    </div>
                </div>
            </FullScreen>
        </div>
    )
}
export default MemoryMain