import React, {useState,useEffect} from 'react';
import { useHistory } from "react-router-dom";
import './Header.scss'
import PopupLogin from "../PopupLogin/PopupLogin";
import PopupRegister from "../PopupRegister/PopupRegister";
import {Link} from 'react-router-dom';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import {faGamepad, faSignOutAlt} from '@fortawesome/free-solid-svg-icons'

const Header = () =>{
    let history = useHistory();
    const [isLoginVisible, setLoginVisible] = useState(false)
    const [isRegisterVisible, setRegisterVisible] = useState(false)

    const loginOpen = () =>{
        setLoginVisible(true)
        setRegisterVisible(false)
    }
    const registerOpen = () =>{
        setRegisterVisible(true)
        setLoginVisible(false)
    }
    const closePopup = () =>{
        setRegisterVisible(false)
        setLoginVisible(false)
    }
    const logout = () =>{
        localStorage.removeItem('userData')
        history.push("/");
    }
    return(
        <header className="App-header">

            <div className="container">
                <div className="header__inner flex-between align-center">
                    <Link exact to="/" className='header-logo'>RSLang</Link>
                    <div className='header__btns'>
                        {!localStorage.getItem("userData") ? (
                                <>
                                    <a href="#" onClick={loginOpen} className='header__sign-in'>Sign In</a>
                                    <a href="#" onClick={registerOpen} className='header__sign-up'>Sign Up</a>
                                </>
                            ) :
                            (
                                <div>
                                    <a href="" onClick={logout} className='btn btn-logout'>
                                        <span>Выйти</span>
                                        <FontAwesomeIcon  icon={faSignOutAlt}  />
                                    </a>
                                </div>
                            )
                        }

                    </div>
                </div>
            </div>
            <div>
                {isLoginVisible &&
                <><div className="popup-container" onClick={closePopup}></div>
                    <PopupLogin hidePopup={closePopup}/>
                </>
                }
                {isRegisterVisible &&
                <><div className="popup-container" onClick={closePopup}></div>
                    <PopupRegister/>
                </>
                }
            </div>



        </header>
    )
}

export default Header;