import React, {useState} from 'react';
import './Profile.scss';
import {Route, Switch, Redirect} from "react-router-dom";

import Sidebar from "../../components/Sidebar/Sidebar";
import Games from "../../components/Games/Games";
import Vocabulary from "../../components/Vocabulary/Vocabulary";
import Education from "../../components/Education/Education";
import Team from '../../components/Team/Team'
import Settings from "../../components/Settings/Settings";
import Statistics from "../../components/Statistics/Statistics";
import StatisticsProvider from '../../components/Statistics/StatisticsContext'


const Profile = () => {
    return (
        <StatisticsProvider>
            <div className='profile-page flex-between'>
                <Sidebar/>
                <div className="profile-inner">
                    <div className="container">

                        <Switch>
                            <Route path="/profile" exact>
                                <Education/>
                            </Route>
                            <Route path="/profile/games">
                                <Games/>
                            </Route>
                            <Route path="/profile/vocabulary">
                                <Vocabulary/>
                            </Route>
                            <Route path="/profile/statistics">
                                <Statistics/>
                            </Route>
                            <Route path="/profile/team">
                                <Team/>
                            </Route>
                            <Route path="/profile/settings">
                                <Settings/>
                            </Route>
                        </Switch>
                    </div>
                </div>
            </div>
        </StatisticsProvider>
    )
}

export default Profile;