import { Link } from 'react-router-dom'
import CloseButton from '../closeButton/CloseButton'
import './MainScreen.scss'
import React from "react";
const audio = new Audio('start.mp3')

const MainScreen = () => {
    return (
        <div className='main-screen'>
        <div className='main-screen__main'>
            <h1 className='main-screen__title'>Саванна</h1>
            <p className='main-screen__description'>Тренировка Саванна развивает словарный запас. Чем больше слов ты знаешь, тем больше очков опыта получишь.</p>
            <Link to='/profile/games/savannah' className='main-screen__button' onClick={() => audio.play()}>Начать</Link>
        </div>
    </div>)
}

export default MainScreen
