import React, {useState, useEffect, useContext} from 'react'
import './GameField.scss'
import Countdown from "../Countdown/Countdown";
import GameContainer from "../GameContainer/GameContainer";
import StatisticGameSprint from "../StatisticGameSprint/StatisticGameSprint";
import GameLevel from '../GameLevel/GameLevel'
import {useSprintStatistics} from '../../Statistics/StatisticsContext'


const GameField = (props) => {
    const [countDownGame, setCountDownGame] = useState(60)
    const [countGame, setCountGame] = useState(0)
    const [isSound, setIsSound] = useState(true)
    const [isVisibleGameField, setIsVisibleGameField] = useState(true)
    const [countCorrect, setCountCorrect] = useState(0)
    const [countUnCorrect, setCountUnCorrect] = useState(0)
    const [arrayCorrectAnswer, setArrayCorrectAnswer] = useState([])
    const [arrayUnCorrectAnswer, setArrayUnCorrectAnswer] = useState([])
    const [counterRightAnswer, setCounterRightAnswer] = useState(0)
    const [isFirstPoint, setFirstPoint] = useState(false)
    const [isSecondPoint, setSecondPoint] = useState(false)
    const [isThirdPoint, setThirdPoint] = useState(false)
    const [isVisiblecountDownGame, setIsVisiblecountDownGame] = useState(false)
    const [isLoader, setIsLoader] = useState(true)
    const [isFirstLevel, setIsFirstLevel] = useState(true)
    const [isSecondLevel, setIsSecondLevel] = useState(false)
    const [isThirdLevel, setIsThirdLevel] = useState(false)
    const [isFourthLevel, setIsFourthLevel] = useState(false)
    const [isFifthLevel, setIsFifthLevel] = useState(false)
    const [isSixthLevel, setIsSixthLevel] = useState(false)
    const [isFirstBonus, setFirstBonus] = useState(false)
    const [isSecondBonus, setSecondBonus] = useState(false)
    const [isThirdBonus, setThirdBonus] = useState(false)
    const [isFirstParrot, setFirstParrot] = useState(false)
    const [isSecondParrot, setSecondParrot] = useState(false)
    const [isThirdParrot, setThirdParrot] = useState(false)
    const [isResultSprint, setRasultSprint] = useState(false)
    const [countAnswer, setCountAnswer] = useState([])

   //statistics provider
    const setSprintStatistics=useSprintStatistics()
    //statistics data
    useEffect(() => {
        setCountAnswer(pref => {
            return [
                ...pref,
                counterRightAnswer
            ]
        })
        // console.log('CountAnswer', countAnswer)
    }, [counterRightAnswer])

    function getMaxOfArray(countAnswer) {
        return Math.max.apply(null, countAnswer);
    }

    let learnedWords = arrayCorrectAnswer.length
    let rightWords = Math.round((arrayCorrectAnswer.length * 100) / (arrayCorrectAnswer.length + arrayUnCorrectAnswer.length))
    let longCorrectAnswer = getMaxOfArray(countAnswer)

    let arrayStatistics = {
        "sprint": {
            "learnedWords": learnedWords,
            "rightWords": rightWords,
            "longCorrectAnswer": longCorrectAnswer
        }
    }

    useEffect(() => {
        if (!isLoader) {
            setIsVisiblecountDownGame(true)
        }
    }, [isLoader])

    useEffect(() => {
        if (countDownGame > 0) {
            setTimeout(() => {
                setCountDownGame(countDownGame - 1)
                if (countDownGame <= 4 && countDownGame > 0 && isSound) {
                    props.audioFinish.play()
                }
            }, 1000);
        } else {
            setIsVisibleGameField(false)
            setCountDownGame(null)
            setRasultSprint(true)
            setSprintStatistics(arrayStatistics)
            if (countDownGame > 4 && countDownGame < 0 && !isVisibleGameField && !isSound) {
                props.audioFinish.pause()
            }
        }
    }, [countDownGame])

    const handlerSound = () => {
        setIsSound(isSound === false ? true : false)
    }
    const audioFalse = new Audio('/audio/false.mp3')
    const audioCorrect = new Audio('/audio/correct.mp3')
    const audioGong = new Audio('/audio/gong.mp3')
    const audioShowResult = new Audio('/audio/show_result.mp3')



    return (
        <>
            {isVisibleGameField ? (<div className={'game_container'}>
                {isVisiblecountDownGame && <div className={'countdown_box'}>
                    <Countdown countDownGame={countDownGame}/>
                </div>}
                <GameLevel isFirstLevel={isFirstLevel}
                           isSecondLevel={isSecondLevel}
                           isThirdLevel={isThirdLevel}
                           isFourthLevel={isFourthLevel}
                           isFifthLevel={isFifthLevel}
                           isSixthLevel={isSixthLevel}
                           isThirdBonus={isThirdBonus}
                           isFirstParrot={isFirstParrot}
                           isSecondParrot={isSecondParrot}
                           isThirdParrot={isThirdParrot}
                           setIsFirstLevel={(value) => setIsFirstLevel(value)}
                           setIsSecondLevel={(value) => setIsSecondLevel(value)}
                           setIsThirdLevel={(value) => setIsThirdLevel(value)}
                           setIsFourthLevel={(value) => setIsFourthLevel(value)}
                           setIsFifthLevel={(value) => setIsFifthLevel(value)}
                           setIsSixthLevel={(value) => setIsSixthLevel(value)}
                           setCountGame={(value) => setCountGame(value)}
                           setCountCorrect={(value) => setCountCorrect(value)}
                           setCountUnCorrect={(value) => setCountUnCorrect(value)}
                           setCounterRightAnswer={(value) => setCounterRightAnswer(value)}
                           setFirstPoint={(value) => setFirstPoint(value)}
                           setSecondPoint={(value) => setSecondPoint(value)}
                           setThirdPoint={(value) => setThirdPoint(value)}
                           setFirstBonus={(value) => setFirstBonus(value)}
                           setSecondBonus={(value) => setSecondBonus(value)}
                           setThirdBonus={(value) => setThirdBonus(value)}
                           setFirstParrot={(value) => setFirstParrot(value)}
                           setSecondParrot={(value) => setSecondParrot(value)}
                           setThirdParrot={(value) => setThirdParrot(value)}

                />
                <div className={'parrot_container'}>
                    <div className={'game_count'}>{countGame}</div>
                    <div className={'sound_icon'} onClick={handlerSound}>{isSound ? (<img
                        src={'data:image/svg+xml;base64,'}
                        alt={'sound'}/>) : (
                        <img
                            src={'data:image/svg+xml;base64,'}
                            alt={'sound'}/>)}</div>
                    <div>
                        <GameContainer isSound={isSound} setCountCorrect={(value) => setCountCorrect(value)}
                                       countCorrect={countCorrect}
                                       countGame={countGame} setCountGame={(value) => setCountGame(value)}
                                       setCountUnCorrect={(value) => setCountUnCorrect(value)}
                                       countUnCorrect={countUnCorrect}
                                       countCorrect={countCorrect} audioCorrect={audioCorrect} audioFalse={audioFalse}
                                       audioGong={audioGong} setArrayCorrectAnswer={setArrayCorrectAnswer}
                                       setArrayUnCorrectAnswer={(value) => setArrayUnCorrectAnswer(value)}
                                       setCounterRightAnswer={(value) => setCounterRightAnswer(value)}
                                       counterRightAnswer={counterRightAnswer}
                                       isFirstLevel={isFirstLevel} isSecondLevel={isSecondLevel}
                                       isThirdLevel={isThirdLevel} isFourthLevel={isFourthLevel}
                                       isFifthLevel={isFifthLevel} isSixthLevel={isSixthLevel}
                                       isFirstPoint={isFirstPoint}
                                       isSecondPoint={isSecondPoint}
                                       isThirdPoint={isThirdPoint}
                                       setFirstPoint={(value) => setFirstPoint(value)}
                                       setSecondPoint={(value) => setSecondPoint(value)}
                                       setThirdPoint={(value) => setThirdPoint(value)}
                                       isFirstParrot={isFirstParrot}
                                       isSecondParrot={isSecondParrot}
                                       isThirdParrot={isThirdParrot}
                                       setFirstParrot={(value) => setFirstParrot(value)}
                                       setSecondParrot={(value) => setSecondParrot(value)}
                                       setThirdParrot={(value) => setThirdParrot(value)}
                                       isFirstBonus={isFirstBonus}
                                       isSecondBonus={isSecondBonus}
                                       isThirdBonus={isThirdBonus}
                                       setFirstBonus={(value) => setFirstBonus(value)}
                                       setSecondBonus={(value) => setSecondBonus(value)}
                                       setThirdBonus={(value) => setThirdBonus(value)}
                                       setIsLoader={(value) => setIsLoader(value)}
                                       isLoader={isLoader}
                                       arrayCorrectAnswer={arrayCorrectAnswer}
                                       arrayUnCorrectAnswer={arrayUnCorrectAnswer}
                        />

                    </div>
                </div>

            </div>) : (
                <StatisticGameSprint isSound={isSound} countUnCorrect={countUnCorrect} countCorrect={countCorrect}
                                     setIsVisibleStartCount={props.setIsVisibleStartCount}
                                     setCountStart={props.setCountStart} countGame={countGame}
                                     audioShowResult={audioShowResult} audioFinish={props.audioFinish}
                                     arrayCorrectAnswer={arrayCorrectAnswer}
                                     arrayUnCorrectAnswer={arrayUnCorrectAnswer}
                                     setCounterRightAnswer={setCounterRightAnswer}
                                     setIsVisibleGameField={props.setIsVisibleGameField}
                                     isResultSprint={isResultSprint}/>)}
        </>

    )
}
export default GameField