import React, { useState, useEffect } from 'react'
import Attempts from "../attempts/Attempts";
import CloseButton from "../closeButton/CloseButton";
import SoundButton from "../soundButton/SoundButton";
import WordsField from "../wordsField/WordsField";
import Diamond from "../diamond/Diamond";
import CurrentWord from "../currentWord/CurrentWord";
import Result from "../result/Result";
import {FullScreen, useFullScreenHandle} from "react-full-screen";
import FullScreenBtn from "../../FullScreenBtn/FullScreenBtn";
import './SavannahGame.scss'

const getRandomNumber = (min, max) => {
    return Math.floor(Math.random() * (max - min + 1)) + min;
}

const uncorrectAnswerSound = new Audio('uncorrect-answer.mp3')
const correctAnswerSound = new Audio('correct-answer.mp3')

const SavannahGame = () => {
    const handle = useFullScreenHandle();
    const [level, setLevel] = useState(1)
    const [attempts, setAttempts] = useState(5)
    const [words, setWords] = useState([])
    const [currentWordIndex, setCurrentWord] = useState(0)
    const [isWordsLoad, setIsWordsLoad] = useState(false)
    const [otherWords, setOtherWords] = useState([1, 2, 3])
    const [isCorrectAnswer, setIsCorrectAnswer] = useState(false)
    const [isUncorrectAnswer, setIsUncorrectAnswer] = useState(false)
    const [backgroundPosition, setBackgroundPosition] = useState(100)
    const [isSoundOn, setIsSoundOn] = useState(true)
    const [correctWords, setCorrectWords] = useState({})
    const [uncorrectWords, setUncorrectWords] = useState({})
    const styles = {
        backgroundPositionY: `${backgroundPosition}%`
    }

    useEffect(() => {
        fetch('https://react-learnwords-example.herokuapp.com/words')
            .then(res => res.json())
            .then(data => {
                setWords(data)
                setIsWordsLoad(true)
            })
    }, [])

    const changeWords = (clickedWord) => {
        const wordsIdexes = []

        while (wordsIdexes.length < 3) {
            const randomIndex = getRandomNumber(0, 19)
            if (!wordsIdexes.includes(randomIndex) && randomIndex !== currentWordIndex + 1) {
                wordsIdexes.push(randomIndex)
            }
        }

        setCurrentWord(currentWordIndex + 1)
        setOtherWords(wordsIdexes)
    }

    const playCorrectSound = () => {
        if (!isSoundOn) return
        correctAnswerSound.currentTime = 0
        correctAnswerSound.play()
    }

    const playUncorrectSound = () => {
        if (!isSoundOn) return
        uncorrectAnswerSound.currentTime = 0
        uncorrectAnswerSound.play()
    }

    const click = (word) => {
        if (word.word === words[currentWordIndex].word) {
            console.log(word)
            playCorrectSound()
            setIsCorrectAnswer(true)
            setBackgroundPosition(backgroundPosition - 5)
            setLevel(level + 1)
            setCorrectWords({...correctWords, [word.word]: word.wordTranslate})
        } else {
            playUncorrectSound()
            setAttempts(attempts - 1)
            setIsUncorrectAnswer(true)
            changeWords()
            setUncorrectWords({...uncorrectWords, [word.word]: word.wordTranslate})
        }
    }

    const uncorrectChange = () => {
        setIsUncorrectAnswer(false)
    }

    const soundSwitch = () => {
        setIsSoundOn(!isSoundOn)
    }

    const correctChange = () => {
        setIsCorrectAnswer(false)
    }

    const attemptsChange = () => {
        setAttempts(attempts - 1)
    }
    if(attempts === 0) {
        return <FullScreen handle={handle}>
            <FullScreenBtn handle={handle}/>
            <div className='main-screen' style={styles}>
                <Result text='В этот раз не получилось, но продолжай тренироваться!'
                        correctWords={correctWords}
                        uncorrectWords={uncorrectWords} />
            </div>
        </FullScreen>

    } else if (currentWordIndex === 19) {
        return <FullScreen handle={handle}>
            <FullScreenBtn handle={handle}/>
            <div className='main-screen' style={styles}>
            <Result text='Поздравляем, отличный результат!'
                    correctWords={correctWords}
                    uncorrectWords={uncorrectWords} />
        </div></FullScreen>
    } else {
    return <FullScreen handle={handle}>
        <FullScreenBtn handle={handle}/>
        <div className='main-screen' style={styles}>
        <SoundButton soundSwitch={soundSwitch} />
        <Attempts attempts={attempts} />
        {/*<CloseButton />*/}
        <CurrentWord word={words[currentWordIndex]}
                     isSoundOn={isSoundOn}
                     uncorrectAnswerSound={uncorrectAnswerSound}
                     click={click}
                     attemptsChange={attemptsChange}
                     isWordsLoad={isWordsLoad}
                     isCorrectAnswer={isCorrectAnswer}
                     isUncorrectAnswer={isUncorrectAnswer}
                     changeWords={changeWords}
                     correctChange={correctChange}
                     uncorrectChange={uncorrectChange} />
        <WordsField trueWord={words[currentWordIndex]}
                    changeWords={changeWords}
                    clickedWord={click}
                    otherWords={[words[otherWords[0]], words[otherWords[1]], words[otherWords[2]]]}
                    isWordsLoad={isWordsLoad} />
        <Diamond level={level}/>
    </div>
    </FullScreen>}
}

export default SavannahGame
