import './Result.scss'

const Result = (props) => {
    const { correctWords, uncorrectWords } = props
    const correctWordsArray = Object.entries(correctWords)
    const uncorrectWordsArray = Object.entries(uncorrectWords)
    const correctWordsList = correctWordsArray.map(([word, translate], index) => {
        return <p key={index}>
            <span className='result__word'>{word}</span> - {translate}
        </p>
    })
    const uncorrectWordsList = uncorrectWordsArray.map(([word, translate], index) => {
        return <p key={index}>
            <span className='result__word'>{word}</span> - {translate}
        </p>
    })

    return <div className='result'>
            <h2>{props.text}</h2>
            <div className='result__words-list'>
                <p className='result__uncorrect'>Ошибок: {uncorrectWordsArray.length}</p>
                <div>{uncorrectWordsList}</div>
                <p className='result__correct'>Знаю: {correctWordsArray.length}</p>
                <div>{correctWordsList}</div>
            </div>

    </div>
}

export default Result
