import React, {useState, useEffect} from 'react';
import { useStore } from '../../store/store';
import {NavLink, Route, Switch} from "react-router-dom";
import './Vocabulary.scss';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faVolumeUp} from '@fortawesome/free-solid-svg-icons'
import VocabularyItem from "./VocabularyItem";


const Vocabulary = () =>{
    const [state] = useStore();
    const userId = JSON.parse(localStorage.getItem("userData")).userId;
    const token = JSON.parse(localStorage.getItem("userData")).token;
    const [wordsArray, setWordsArray]= useState([]);
    const [wordsDiffArray, setWordsDiffArray]= useState([]);
    const [wordsDeleteArray, setWordsDeleteArray]= useState([]);
    const [data, setData] = useState(null);
    let [wordsItems, setWordsItems] = useState(null)

    const useFetch = () => {


        // empty array as second argument equivalent to componentDidMount
        useEffect(() => {
            const getUserWords = async ( userId ) => {
                const rawResponse = await fetch(`https://react-rslang-de.herokuapp.com/users/${userId}/words/`, {
                    method: 'GET',
                    withCredentials: true,
                    headers: {
                        'Authorization': `Bearer ${token}`,
                        'Accept': 'application/json',
                        'Content-Type': 'application/json'
                    },
                });
                const content = await rawResponse.json();
                setData(content);

            };
            getUserWords(userId)
        }, []);

        return data;
    };


    const getWordById = async ( userId) => {
     //   setWordsArray([])
        const rawResponse = await fetch(`https://react-rslang-de.herokuapp.com/users/${userId}/aggregatedWords`, {
            method: 'GET',
            withCredentials: true,
            headers: {
                'Authorization': `Bearer ${token}`,
                'Accept': 'application/json',
                'Content-Type': 'application/json'
            },
        });
        const content = await rawResponse.json();
        const paginatedResults = content[0].paginatedResults

        setWordsArray(  paginatedResults);
    };
    const result = useFetch();
    useEffect(()=>{
        if(result !== null){
            getWordById(userId)
        }
    }, [result])

    useEffect(() => {
      // console.log(wordsArray)
    }, [wordsArray]);

    const getDiffWords = async ( userId) => {
        const data = { learnedWords: 10,
            optional: {}};

        const rawResponse = await fetch(`https://react-rslang-de.herokuapp.com/users/${userId}/aggregatedWords?filter=%7B"%24and"%3A%5B%7B"userWord.difficulty"%3A"difficult"%2C"userWord.optional.mode"%3A%7B"%24not"%3A%7B"%24eq"%3A"deleted"%7D%7D%7D%5D%7D&wordsPerPage=3600`, {
            method: 'GET',
            withCredentials: true,
            headers: {
                'Authorization': `Bearer ${token}`,
                'Accept': 'application/json',
                'Content-Type': 'application/json',
            },

        });
        const content = await rawResponse.json();
        const paginatedResults = content[0].paginatedResults

        setWordsDiffArray(paginatedResults)
        console.log(wordsDiffArray)

    };
    const getDeletedWords = async ( userId) => {
        const data = { learnedWords: 10,
            optional: {}};

        const rawResponse = await fetch(`https://react-rslang-de.herokuapp.com/users/${userId}/aggregatedWords?filter=%7B"userWord.optional.mode"%3A%7B"%24eq"%3A"deleted"%7D%7D&wordsPerPage=3600`, {
            method: 'GET',
            withCredentials: true,
            headers: {
                'Authorization': `Bearer ${token}`,
                'Accept': 'application/json',
                'Content-Type': 'application/json',
            },

        });
        const content = await rawResponse.json();
        const paginatedResults = content[0].paginatedResults

        setWordsDeleteArray(paginatedResults)
        console.log(wordsDiffArray)

    };
    useEffect(() => {
        getDiffWords(userId)
        getDeletedWords(userId)

    }, []);

    return(
        <div className='vocabulary'>
            <h1>Словарь</h1>
            <p>Список изученных слов за период обучения</p>
            <ul className='flex-start flex-wrap'>
                <li>
                    <NavLink exact activeClassName="selected" to="/profile/vocabulary">Изучаемые слова </NavLink>
                </li>
                <li>
                    <NavLink   activeClassName="selected" to="/profile/vocabulary/difficult">Сложные слова </NavLink>
                </li>
                <li>
                    <NavLink  activeClassName="selected"  to="/profile/vocabulary/delete">Удаленные слова </NavLink>
                </li>
            </ul>

            <div className="words">
                <Switch>
                    <Route path="/profile/vocabulary" exact>
                        <h2>Изучаемые:</h2>
                        <div className="word-item">
                            {
                                wordsArray.length > 0 ?  wordsArray.map((item, index) =>
                                        <VocabularyItem sitePath={state.sitePath} item={item} index={index} />
                                    )
                                    : (
                                        <div>Изученных слов не найдено.</div>
                                    )
                            }

                        </div>
                    </Route>
                    <Route path="/profile/vocabulary/difficult" >
                        <h2>Сложные:</h2>
                        {
                            wordsDiffArray.length > 0 ?  wordsDiffArray.map((item, index) =>
                                    <VocabularyItem sitePath={state.sitePath} item={item} index={index} />
                                )
                                : (
                                    <div>Сложных слов не найдено.</div>
                                )
                        }

                    </Route>
                    <Route path="/profile/vocabulary/delete" >
                        <h2>Удаленные:</h2>
                        {
                            wordsDeleteArray.length > 0 ?  wordsDeleteArray.map((item, index) =>
                                    <VocabularyItem sitePath={state.sitePath} item={item} index={index} />
                                )
                                : (
                                    <div>Удаленных слов не найдено.</div>
                                )
                        }
                    </Route>
                </Switch>


            </div>
        </div>
    )
}
export default Vocabulary;