import React, {useEffect, useState} from 'react'
import './ShortTermStatistics.scss'
import {Redirect} from "react-router-dom";

const ShortTermStatistics = (props) => {
let rightLearnedWord
    if(props.userLearnedWords){
        rightLearnedWord =(props.userLearnedWords*100)/20
    }


    return (
        <>
            <div className={'title_statistics'}>
                <h4>Изучено сегодня</h4>
            </div>

            <table border={1}>
                <tr>
                    <th>Игра</th>
                    <th>Количество изученных слов</th>
                    <th>Правильные слова, %</th>
                    <th>Самая длинная серия правильных ответов</th>
                </tr>

                <tr>
                    <td>Спринт</td>
                    <td>{props.sprintData && props.sprintData.learnedWords}</td>
                    <td>{props.sprintData && props.sprintData.rightWords}</td>
                    <td>{props.sprintData && props.sprintData.longCorrectAnswer}</td>
                </tr>
                <tr>
                    <td>Аудиовызов</td>
                    <td>{props.audioData && props.audioData.learnedWords}</td>
                    <td>{props.audioData && props.audioData.rightWords}</td>
                    <td>{props.audioData && props.audioData.longCorrectAnswer}</td>
                </tr>
                <tr>
                    <td>Саванна</td>
                    <td>{props.savannaData && props.savannaData.learnedWords}</td>
                    <td>{props.savannaData && props.savannaData.rightWords}</td>
                    <td>{props.savannaData && props.savannaData.longCorrectAnswer}</td>
                </tr>
                <tr>
                    <td>Своя игра</td>
                    <td>{props.memoryData && props.memoryData.learnedWords}</td>
                    <td>{props.memoryData && props.memoryData.rightWords}</td>
                    <td>{props.memoryData && props.memoryData.longCorrectAnswer}</td>
                </tr>
                <tr>
                    <td className={'all_answers'}>Общее количество</td>
                    <td>{props.userLearnedWords&&props.userLearnedWords}</td>
                    <td>{rightLearnedWord}</td>

                </tr>

            </table>
        </>
    )
}
export default ShortTermStatistics