import React, {useState, useEffect} from 'react';
import {Redirect} from "react-router-dom";
import useFetch from '../../hooks/useFetch';
import { useStore } from '../../store/store';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faVolumeUp, faGraduationCap} from '@fortawesome/free-solid-svg-icons'
import './Education.scss';
const Education = () =>{
    const [, dispatch] = useStore();
    const [state] = useStore();
    const [dataIndex, setDataIndex] = useState(0);
    const [IsShowAnswer, setShowAnswer] = useState(false);
    const [showSuccessMessage, setShowSuccessMessage] = useState(false);
    const [showErrorMessage, setShowErrorMessage] = useState(false);
    const [enterWord, setEnterWord] = useState('')
    const [learnedWords, setlearnedWords] = useState(0)

    const [pageNumber, setPageNumber] = useState(1)
    const [groupNumber, setGroupNumber] = useState(0)
    const [IsGroupSelected, setGroupSelected] = useState(false)
    const [IsPageSelected, setPageSelected] = useState(false)
    const URL = `https://react-rslang-de.herokuapp.com/words?page=${pageNumber}&group=${groupNumber}`;
    const result = useFetch(URL);
    const userId = JSON.parse(localStorage.getItem("userData")) ? JSON.parse(localStorage.getItem("userData")).userId : null;
    if(userId === null){
       return <Redirect  to="/" />
    }
    const token = JSON.parse(localStorage.getItem("userData")).token;
    const sitePath = 'https://react-rslang-de.herokuapp.com/';
    let difficulty = "weak"
    let isDeletedWord = ''
    let learnWord = '';
    let wordAudio = '';
    let word = {

    }

    if(result !== null){
        if(dataIndex < 19){
            learnWord = result[dataIndex]
            wordAudio = sitePath + learnWord.audio
            word = {
                id: learnWord.id,
                difficultWord: false,
                deleted: false
            }
        }

    }
    let audio = new Audio(wordAudio)
    const showAnswer = () =>{
        setShowAnswer(true)

        audio.play()
    }
    const playAudioMeaning = (audioMeaning) =>{
        const audio = new Audio(sitePath + audioMeaning)
        audio.play()
    }
    const checkAnswer = () =>{

        if(result !== null){
            if(enterWord.toLowerCase() == learnWord.word.toLowerCase()){
                setShowSuccessMessage(true)
                setlearnedWords(learnedWords + 1)

            } else{
               setShowErrorMessage(true)
            }
            audio.play()
            setShowAnswer(true)
        }


    }

    const addUserWord = async userWord => {
        const ajaxUrl = 'https://react-rslang-de.herokuapp.com/users/' +userId + '/words/' + learnWord.id;
        const rawResponse = await fetch(ajaxUrl, {
            method: 'POST',
            headers: {
                'Accept': 'application/json',
                'Content-Type': 'application/json'
            },
            body: JSON.stringify(userWord)
        });
        const content = await rawResponse.json();

        console.log(content);
    };


    const createUserWord = async ({ userId, wordId, word }) => {
        const rawResponse = await fetch(`https://react-rslang-de.herokuapp.com/users/${userId}/words/${wordId}`, {
            method: 'POST',
            withCredentials: true,
            headers: {
                'Authorization': `Bearer ${token}`,
                'Accept': 'application/json',
                'Content-Type': 'application/json'
            },
            body: JSON.stringify(word)
        });
        const content = await rawResponse.json();

        console.log(content);
    };


    const getNextWord = () =>{
        if(enterWord.toLowerCase() == learnWord.word.toLowerCase()){
            createUserWord({
                userId: userId,
                wordId: learnWord.id,
                word: { "difficulty": difficulty, "optional": {testFieldString: 'test', mode: isDeletedWord} }
            });
        }

        setDataIndex(dataIndex + 1)
        setShowAnswer(false)
        setEnterWord('')
        setShowSuccessMessage(false)
        setShowErrorMessage(false)

          console.log(state)
    }
    const isDifficultWord = () =>{
        difficulty = "difficult"
    }
    const isDeletedHandled = () =>{
        isDeletedWord = 'deleted'
    }

    const setGroup = (number) =>{
        setGroupNumber(number - 1)
        setGroupSelected(true)
    }
    const setPage = (number) => {
        setPageNumber(number - 1)
        setPageSelected(true)
    }

    const setNewGroup = () =>{
        setDataIndex(0)
    }
    return(
        <div>{dataIndex < 19 ?
            <>
                <h1>Обучение</h1>

                {
                    !IsGroupSelected &&(
                        <div>
                            <div className="h2 text-center">Выберите раздел</div>
                            <div className='education-wrapper flex flex-wrap'>

                                <a href='#' onClick={() =>setGroup(1)} className="education-item flex flex-column">
                                    <FontAwesomeIcon icon={faGraduationCap}/>
                                    <span>Раздел 1</span>
                                </a>
                                <a href='#' onClick={() => setGroup(2)}  className="education-item flex flex-column">
                                    <FontAwesomeIcon icon={faGraduationCap}/>
                                    <span>Раздел 2</span>
                                </a>
                                <a href='#' onClick={() => setGroup(3)}  className="education-item flex flex-column">
                                    <FontAwesomeIcon icon={faGraduationCap}/>
                                    <span>Раздел 3</span>
                                </a>
                                <a href='#' onClick={() => setGroup(4)}  className="education-item flex flex-column">
                                    <FontAwesomeIcon icon={faGraduationCap}/>
                                    <span>Раздел 4</span>
                                </a>
                                <a href='#' onClick={ () => setGroup(5)}  className="education-item flex flex-column">
                                    <FontAwesomeIcon icon={faGraduationCap}/>
                                    <span>Раздел 5</span>
                                </a>
                                <a href='#' onClick={() => setGroup(6)}  className="education-item flex flex-column">
                                    <FontAwesomeIcon icon={faGraduationCap}/>
                                    <span>Раздел 6</span>
                                </a>
                            </div>
                        </div>
                    )
                }
                {IsGroupSelected && !IsPageSelected && (
                    <div>
                        <div className="h2 text-center">Выберите страницу</div>
                        <div className='education-wrapper flex flex-wrap'>

                            <a href="#" onClick={() => setPage(1)} className='education-page'>1</a>
                            <a href="#" onClick={() => setPage(2)} className='education-page'>2</a>
                            <a href="#" onClick={() => setPage(3)} className='education-page'>3</a>
                            <a href="#" onClick={() => setPage(4)} className='education-page'>4</a>
                            <a href="#" onClick={() => setPage(5)} className='education-page'>5</a>
                            <a href="#" onClick={() => setPage(6)} className='education-page'>6</a>
                            <a href="#" onClick={() => setPage(7)} className='education-page'>7</a>
                            <a href="#" onClick={() => setPage(8)} className='education-page'>8</a>
                            <a href="#" onClick={() => setPage(9)} className='education-page'>9</a>
                            <a href="#" onClick={() => setPage(10)} className='education-page'>10</a>
                            <a href="#" onClick={() => setPage(11)} className='education-page'>11</a>
                            <a href="#" onClick={() => setPage(12)} className='education-page'>12</a>
                            <a href="#" onClick={() => setPage(13)} className='education-page'>13</a>
                            <a href="#" onClick={() => setPage(14)} className='education-page'>14</a>
                            <a href="#" onClick={() => setPage(15)} className='education-page'>15</a>
                            <a href="#" onClick={() => setPage(16)} className='education-page'>16</a>
                            <a href="#" onClick={() => setPage(17)} className='education-page'>17</a>
                            <a href="#" onClick={() => setPage(18)} className='education-page'>18</a>
                            <a href="#" onClick={() => setPage(19)} className='education-page'>19</a>
                            <a href="#" onClick={() => setPage(20)} className='education-page'>20</a>
                            <a href="#" onClick={() => setPage(21)} className='education-page'>21</a>
                            <a href="#" onClick={() => setPage(22)} className='education-page'>22</a>
                            <a href="#" onClick={() => setPage(23)} className='education-page'>23</a>
                            <a href="#" onClick={() => setPage(24)} className='education-page'>24</a>
                            <a href="#" onClick={() => setPage(25)} className='education-page'>25</a>
                            <a href="#" onClick={() => setPage(26)} className='education-page'>26</a>
                            <a href="#" onClick={() => setPage(27)} className='education-page'>27</a>
                            <a href="#" onClick={() => setPage(28)} className='education-page'>28</a>
                            <a href="#" onClick={() => setPage(29)} className='education-page'>29</a>
                            <a href="#" onClick={() => setPage(30)} className='education-page'>30</a>


                        </div>
                    </div>
                )}

                {IsGroupSelected && IsPageSelected && result !== null && (
                    <div>
                        <div className="word-card">
                            {state.settings.showTranslateWord && (
                                <div>
                                    <div>
                                        <div className="h2">{learnWord.wordTranslate}</div>
                                    </div>
                                    <hr/>
                                </div>
                            )}

                            <div className='flex align-center'>
                                <div dangerouslySetInnerHTML={{__html:learnWord.textMeaning}}>
                                </div>
                                <FontAwesomeIcon onClick={()=> playAudioMeaning(learnWord.audioMeaning)}  icon={faVolumeUp}/>
                            </div>

                            <hr/>
                            {state.settings.showTranslateMeaning && (
                                <div>
                                    {learnWord.textMeaningTranslate}
                                </div>
                            )}

                            <input value={enterWord} onChange={e => setEnterWord(e.target.value)} type="text" placeholder="Введите ответ"/>
                            <div>
                                {showSuccessMessage &&(
                                    <div className='success'>Правильно!</div>
                                )}
                                {showErrorMessage &&(
                                    <div className='error'>Неправильно!</div>
                                )}
                            </div>
                            {IsShowAnswer &&
                            (
                                <div>
                                    <hr/>
                                    <div className="flex-between flex-wrap">
                                        <div>
                                            {learnWord.word} {learnWord.transcription}
                                        </div>
                                        <div>
                                            {learnWord.wordTranslate}
                                        </div>
                                    </div>
                                </div>
                            )
                            }

                        </div>
                        {!IsShowAnswer && (
                            <div className="flex flex-wrap">
                                <a href="#" onClick={checkAnswer} className='btn btn-check'>Проверить</a>
                                <a href="#" onClick={showAnswer} className='btn btn-answer'>Показать ответ</a>
                            </div>
                        )}
                        {IsShowAnswer &&(
                            <div className="flex flex-wrap">
                                {state.settings.showDeleteDiffWords && (
                                    <>
                                        <a href="#" onClick={isDeletedHandled}  className='btn btn-delete'>Удалить</a>
                                        <a href="#" onClick={isDifficultWord}  className='btn btn-difficult'>Сложно?</a>
                                    </>
                                )}

                                <a href="#" onClick={getNextWord} className='btn btn-next'>Следующее слово</a>
                            </div>
                        )}
                        <div className='learned-words'>
                            Выучено слов: {learnedWords}/20
                        </div>
                    </div>


                )}
            </>
            : <div className='text-center education-finish'>
                Вы прошли все слова в этой главе! <b><a onClick={setNewGroup} href="">Выберете новые слова</a></b>

            </div>
        }


        </div>
    )
}
export default Education;